.history_section {
	padding: 0;
}

.history_section__title {
	margin-bottom: 8.8rem;
	padding: 0 var(--gl-indent);

	font-weight: 300;
	font-size: 4.8rem;
	text-align: center;
	color: $accent_color_1;
}

.history_section__storyline {
	margin-bottom: 8rem;
	
	@include bp($point_2, min) {
		padding: 0 19.5rem;
	}

	@include bp($point_3) {
		padding: 0 var(--gl-indent);
	}
}

.history_section__scrollbar_w {
	@include bp($point_2, min) {
		padding: 0 25.7rem;
	}

	@include bp($point_3) {
		padding: 0 var(--gl-indent);
	}
}