.popup_key_people {
	z-index: 100;
	position: fixed;
	top: 0;
	left: 0;

	width: 100%;
	height: 100%;

	visibility: hidden;
	overflow-x: hidden;
	overflow-y: auto;

	background: rgba(0,0,0,0.8);
	transition: all 0.5s ease;

	&.popup--open {
		visibility: visible;
		opacity: 1;
	}
}

/*----------Decor---------*/
.popup_key_people__decor {
	z-index: -1;
	position: absolute;
	bottom: 0;

	display: block;
	margin: 0 10% 20% 0;

	&:nth-child(3) {
		width: 33.2rem;
		height: 36.6rem;
		left: 15.4rem;
		margin: 0 0 3.2% 0;
	}
	&:nth-child(2) {
		width: 13rem;
		height: 13rem;
		left: 0;
		margin: 0 0 -2% 0;
	}
	&:nth-child(1) {
		width: 23rem;
		height: 25rem;
		left: 37rem;
		margin: 0 0 -5.6% 0;
	}
}

.popup_key_people__decor_img {
	@extend %contain_img;
}
/*----------Decor###------*/

.popup_key_people__in {
	@extend %global_flex_block_row_wrap_center_center;
	//min-height: calc(100vh - var(--height-header));
	width: 100%;
	// max-width: 192rem;
	// margin: 0 auto;
}

.popup_key_people__container {
	position: relative;
	
	width: 100%;
	min-height: 100vh;

	background: $white;
	opacity: 0;
	transform: scale(.5, .5);
	transition: opacity .5s ease, transform .5s ease;
	overflow: hidden;

	.popup--open & {
		transform: scale(1, 1);
		opacity: 1;
	}

	@include flex_block(row, nowrap, center, center, center);

	@include bp($point_2, min) {
		padding: 16.4rem var(--gl-indent);
	}

	@include bp($point_3) {
		padding: 9.4rem var(--gl-indent);
	}
}

.popup_key_people__container_in {
	width: 100%;
	max-width: 119rem;
	margin: 0 auto;

	@include flex_block(row, wrap, flex-start, flex-start);
}

.popup_key_people__close {
	position: absolute;

	cursor: pointer;

	@include bp($point_2, min) {
		top: 1rem;
		right: 4.8rem;
		width: 6rem;
		height: 6rem;
	}

	@include bp($point_3) {
		top: 1.8rem;
		right: 1.8rem;
		width: 5rem;
		height: 5rem;
	}
}

.popup_key_people__close_hexagon {
	width: 100%;
	height: 100%;
}

.popup_key_people__close_cross {
	@extend %transition_color;
	position: absolute;
	top: 50%;
	left: 50%;
	transform: translate(-50%, -50%);

	color: $white;

	@include bp($point_2, min) {
		width: 2.2rem;
		height: 2.2rem;
	}

	@include bp($point_3) {
		width: 1.8rem;
		height: 1.8rem;
	}

	.popup_key_people__close:hover & {
		color: $accent_color_4;
	}
}

.popup_key_people__pic {
	@include bp($point_2, min) {
		width: 49.6%;
		padding-right: 9rem;
	}

	@include bp($point_3) {
		width: 100%;
		margin-bottom: 3rem;
	}
}

.popup_key_people__img {
	display: block;
	width: 100%;

	@include bp($point_3) {
		max-width: 45rem;
		margin: 0 auto;
	}
}

.popup_key_people__img_in {
	@extend %cover_img;
}

.popup_key_people__content {
	@include bp($point_2, min) {
		width: 50.4%;
		padding-top: 10rem;
	}

	@include bp($point_3) {
		width: 100%;
	}
}

.popup_key_people__title {
	@extend %h1;
	margin-bottom: .2rem;
	color: $accent_color_2;
}

.popup_key_people__subtitle {
	@extend %subtitle;

	@include bp($point_2, min) {
		margin-bottom: 3.8rem;
	}

	@include bp($point_3) {
		margin-bottom: 2rem;
	}
}

.popup_key_people__descrip {
	@extend %descrip;

	p:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 2rem;
		}
		@include bp($point_3) {
			margin-bottom: 1.2rem;
		}
	}
}
