.scrollbar {
	position: relative;

	height: 2rem;

	cursor: pointer;

	user-select: none;

	&.scrollbar--infinite_line_mod {
		overflow: visible;
	}

	&::before {
		position: absolute;
		top: 50%;
		left: 0;
		
		display: block;
		height: 1px;
		width: 100%;
		
		background-color: #d8d8d8;
	
		content: '';
	}

	&.scrollbar--infinite_line_mod::before {
		left: -1000rem;
		width: 2000rem;
	}
}

.scrollbar__indicator {
	@extend %global_flex_block_row_nowrap_center_center;

	position: absolute;
	top: 0;
	left: 0;

	margin: 0 -1.9rem;
	width: 3.8rem;
	height: 2rem;

	background-color: $white;
	cursor: grab;
}

.scrollbar__indicator_icon {
	width: 1.7rem;
	height: 1.8rem;

	color: $accent_color_1;
}