.prodotto_scheda_final_product {
	@include bp($point_2, min) {
		padding: 15rem var(--gl-indent) 26.2rem;
	}

	@include bp($point_3) {
		padding: 10rem var(--gl-indent) 12rem;
	}
}

.section__in {
	.prodotto_scheda_final_product & {
		max-width: 156rem;
	}
}

.final_product__title {
	@extend %h1;

	&:not(:last-child) {
		margin-bottom: 18.8rem;
	}
}

%final_product_slider__mask {
	z-index: 5;
	position: absolute;
	top: 0;

	display: block;
	height: 100%;

	content: '';



	@include bp($point_3) {
		width: 6.2rem;
	}
}

.final_product_slider {
	position: relative;

	@include bp($point_2, min) {
		margin-left: -9.8rem;
	}

	&:after {
		@extend %final_product_slider__mask;

		left: -1px;

		@include bp($point_2, min) {
			width: 24rem;
			background: linear-gradient(270deg, rgba(246,246,246,0) 0%, $body_bg calc(100% - 9.8rem), $body_bg 100%);
		}

		@include bp($point_3) {
			background: linear-gradient(270deg, rgba(246,246,246,0) 0%, $body_bg 100%);
		}
	}

	&:before {
		@extend %final_product_slider__mask;
		background: linear-gradient(90deg, rgba(246,246,246,0) 0%, $body_bg 100%);
		right: -1px;

		@include bp($point_2, min) {
			width: 13.2rem;
		}
	}
}

.swiper-slide {
	.final_product_slider & {
		height: auto;
	}
}

.final_product_slider__wrap {
	display: flex;
	align-items: stretch;
}

.final_product__link {
	@include flex_block(column, wrap, space-between, center, center);

	width: 100%;
	height: 100%;

	color: currentColor;

	&:hover,
	&:active {
		color: currentColor;
		text-decoration: none;
	}
}

.final_product__img_wrap {
	@extend %global_flex_block_row_wrap_center_center;
	flex: auto;
}

.final_product__img {
	width: auto;
	height: auto;
	margin-bottom: 2.6rem;
}

.final_product__img_in {
	@extend %transition_transform;
	@extend %contain_img;

	.final_product__link:hover & {
		transform: scale(1.1);
	}
}

.final_product__name {
	margin-top: auto;

	font-weight: 300;
	line-height: 1.4;

	@include bp($point_2, min) {
		font-size: 2rem;
	}

	@include bp($point_3) {
		font-size: 1.6rem;
	}
}

.final_product_slider__btn {
	@extend %transition_transform;

	position: absolute;
	top: 50%;
	margin-top: -2.6rem;
	color: $color_text_base;
	cursor: pointer;
	z-index: 40;

	width: 3.6rem;
	height: 1.8rem;
}

.final_product_slider__btn--prev {

	@include bp($point_2, min) {
		left: 13.8rem;
	}
	@include bp($point_3) {
		left: 0;
	}
	&:hover {
		transform: translateX(-6px);
	}
}

.final_product_slider__btn--next {

	@include bp($point_2, min) {
		right: 4rem;
	}

	@include bp($point_3) {
		right: 0;
	}
	&:hover {
		transform: translateX(6px);
	}
}
