//---------------------------------------------Headings
%h0 {
	color: $accent_color_1;
	font-weight: 300;

	@include bp($point_2, min) {
		font-size: 6.8rem;
	}

	@include bp($point_3) {
		font-size: 4.6rem;
	}
}

%h1 {
	color: $accent_color_1;
	font-weight: 300;
	@include bp($point_2, min) {
		font-size: 4.8rem;
	}

	@include bp($point_3) {
		font-size: 3.4rem;
	}
}

%subtitle {
	font-weight: 700;
	line-height: .9;
	text-transform: uppercase;

	opacity: 0.5;
	color: $accent_color_4;

	@include bp($point_2, min) {
		font-size: 1.4rem;
	}

	@include bp($point_3) {
		font-size: 1.2rem;
	}
}

%descrip {
	font-weight: 300;
	line-height: 1.5;

	@include bp($point_2, min) {
		font-size: 2rem;
	}

	@include bp($point_3) {
		font-size: 1.6rem;
	}
}

%title_offset {
	margin-bottom: 2rem;
}
//---------------------------------------------Headings###

//---------------------------------------------Text
%base_text_style {
	color: $accent_color_2;
	font-size: 2rem;
	font-weight: 300;
	line-height: 1.5;
}
//---------------------------------------------Text###
