//---------------------------------------------lang_dropdown
$lang_dropdown_width: 14.4rem;

.lang_dropdown {
	position: absolute;
	top: 100%;
	left: 50%;

	width: $lang_dropdown_width;
	margin-left: $lang_dropdown_width / 2 * -1;
	padding-top: 2.6rem;

	opacity: 0;
	transition: opacity .3s ease;
	pointer-events: none;

	.header__menu_item--dropdown_mod:hover & {
		opacity: 1;
		pointer-events: auto;
	}
}

.lang_dropdown__in {
	background-color: $white;
	box-shadow: 0 1.2rem 1.2rem rgba(0, 0, 0, 0.1);
}

.lang_dropdown__link {
	display: block;
	padding: 1.2rem;

	color: $accent_color_1;
	font-size: 1.6rem;
	font-weight: 300;
	text-transform: uppercase;
	text-align: center;

	background-color: $white;
	transition: background-color .3s ease;

	&:hover,
	&:focus {
		background-color: darken($white, 3%);
		text-decoration: none;
	}
}

//---------------------------------------------lang_dropdown###