.related_applications {
	background-color: $accent_color_3;

	@include bp($point_2, min) {
		padding-top: 27rem;
		padding-bottom: 25rem;
	}
}

.related_applications__in {
	max-width: $page_width_2;
}

.related_applications__title {
	@extend %h1;
	@include bp($point_2, min) {
		margin-bottom: 19rem;
	}

	@include bp($point_3) {
		margin-bottom: 5rem;
	}
}

%related_applications_slider__mask {
	z-index: 5;
	position: absolute;
	top: 0;

	display: block;
	height: 100%;

	content: '';

	@include bp($point_2, min) {
		width: 13.2rem;
	}

	@include bp($point_3) {
		width: 6.2rem;
	}
}


.related_applications_slider {
	position: relative;

	&:after {
		@extend %related_applications_slider__mask;
		background: linear-gradient(270deg, rgba(246,246,246,0) 0%, #f6f6f6 100%);
		left: -1px;
	}

	&:before {
		@extend %related_applications_slider__mask;
		background: linear-gradient(90deg, rgba(246,246,246,0) 0%, #f6f6f6 100%);
		right: -1px;
	}
}

.swiper-slide {
	.related_applications & {
		height: auto;
	}
}

.related_applications_slider__list {
	display: flex;
	align-items: stretch;
}

.related_applications_slider__item {

}

.related_applications_slider__link {
	height: 100%;
	width: 100%;

	&:focus,
	&:hover {
		text-decoration: none;
	}

	@include bp($point_2, min) {
		@include flex_block(column, wrap, space-between, center, flex-start);
	}

	@include bp($point_3) {
		@include flex_block(column, wrap, space-between, center, center);
	}
}

.related_applications_slider__pic {
	@extend %global_flex_block_row_wrap_center_center;
	flex: auto;
}

.related_applications_slider__img {
	width: auto;
	height: auto;
	margin-bottom: 2.6rem;
}

.related_applications_slider__img_in {
	@extend %transition_transform;
	@extend %cover_img;

	.related_applications_slider__link:hover & {
		transform: scale(1.1);
	}
}

.related_applications_slider__descrip {
	@extend %descrip;
	color: $accent_color_2;
	text-align: center;
}

.related_applications_slider__arrow {
	@extend %transition_transform;
	z-index: 10;
	position: absolute;
	top: 50%;
	margin-top: -2.6rem;

	cursor: pointer;

	&.related_applications_slider__arrow--prev {

		&:hover {
			transform: translateX(-6px);
		}
		@include bp($point_2, min) {
			left: 4.6rem;
		}

		@include bp($point_3) {
			left: 0;
		}
	}

	&.related_applications_slider__arrow--next {

		&:hover {
			transform: translateX(6px);
		}

		@include bp($point_2, min) {
			right: 4.6rem;
		}

		@include bp($point_3) {
			right: 0;
		}
	}

	@include bp($point_2, min) {
		width: 3.6rem;
		height: 1.8rem;
	}

	@include bp($point_3) {
		width: 2.6rem;
		height: 1.8rem;
	}
}
