.applicazioni_prodotto_products {
	@include bp($point_2, min) {
		padding: 15rem var(--gl-indent) 17.4rem;
	}


	@include bp($point_3) {
		padding: 10rem var(--gl-indent) 12rem;
	}
}

.section__in {
	.applicazioni_prodotto_products & {
		max-width: 156rem;
	}
}

.products__wrap {
	width: 100%;

	@include bp($point_2, min) {
		@include flex_block(row, wrap, flex-start);
	}

}

.products__caption {
	@include bp($point_2, min) {
		flex: 0 0 35.85%;

		padding: 11.6rem 2rem 2rem 0;
	}

	@include bp($point_3) {
		padding: 5rem 0;
	}
}


.products__title {
	@extend %h1;
}

.products__body {
	flex: 1 0 47%;
}

.products__list {

	margin-bottom: -7rem;
}

.products__item {
	padding-bottom: 7rem;

	@include bp($point_5, min) {
		@include flex_block(row, nowrap, flex-start);
	}
}

.products__img_wrap {

	@include bp($point_2, min) {
		margin-right: 8.6rem;
	}

	@include bp($point_3) {
		margin-right: 3.6rem;
	}

	@include bp($point_5, min) {
		flex: 0 0 20.2rem;
		height: 24.4rem;
	}

	@include bp($point_5 - 1) {
		width: 50%;
		margin-left: auto;
		margin-right: auto;
		&:not(:last-child) {
			margin-bottom: 3rem;
		}
	}
}

.products__info {
	@include bp($point_5, min) {
		padding-top: 5.6rem ;
	}
}

.products__descr {
	&:not(:last-child) {
		margin-bottom: 3.2rem;
	}
}

.products__img_mask {
	width: 100%;
}

.products__img {
	width: 100%;
	height: 100%;
}

.products__img_in {
	@extend %contain_img;
}

.products__category {

	font-weight: 300;
	line-height: 1.35;

	&:not(:last-child) {
		margin-bottom: .8rem;
	}

	@include bp($point_2, min) {
		font-size: 4.8rem;
	}

	@include bp($point_3) {
		font-size: 3.4rem;
	}
}

.products__descr_text {
	@extend %descrip;
}

.products__link {
	@extend %transition_all;
	display: block;
	width: 16.6rem;

	color: currentColor;

	cursor: pointer;

	&:hover,
	&:active {
		width: 18.6rem;
		text-decoration: none;
		color: $accent_color_1;
	}
}


.products__link_text {
	font-weight: 700;
	line-height: 1.1;
	letter-spacing: 0.14em;
	text-transform: uppercase;

	@include bp($point_2, min) {
		font-size: 1.4rem;
	}

	@include bp($point_3) {
		font-size: 1.2rem;
	}
}

.products__link_icon {
	width: auto;
	height: 1.8rem;
}
