.hero {
	@extend %global_flex_block_row_wrap_flex-start_center;

	position: relative;
	min-height: 100vh;

	@include bp($point_2, min) {
		// height: 100vh;
		padding: 10rem var(--gl-indent);
	}
	@include bp($point_3) {
		padding: 86vw var(--gl-indent) 10rem;
	}
}

.hero__in {
	max-width: $page_width_2;
	height: 100%;

	@include bp($point_2, min) {
		@include flex_block(row, nowrap, flex-start, center, center);
	}
}

.hero__content {
	max-width: clamp(43rem, 38vw, 60rem);
	width: 100%;
	@include bp($point_2, min) {
		margin-right: 3.4rem;
	}
	@include bp($point_3) {
		text-align: center;
		margin: 0 auto;
	}
}

.hero__title {
	@extend %h1;

	color: $accent_color_2;

	@include bp($point_2, min) {
		line-height: 1;

		&:not(:last-child) {
			margin-bottom: 3.4rem;
		}
	}
	@include bp($point_3) {
		font-size: 2.4rem;
		line-height: 1.3;

		&:not(:last-child) {
			margin-bottom: 1.8rem;
		}
	}

	strong {
		display: block;
		font-weight: 300;
		color: $accent_color_1;

		@include bp($point_2, min) {
			font-size: 6.8rem;
		}
		@include bp($point_3) {
			font-size: 3.4rem;
		}
	}
}

.hero__descr {
	@extend %base_text_style;

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 3.2rem;
		}
	}
	@include bp($point_3) {
		font-size: 1.6rem;

		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}
}

.hero__links {
	position: relative;

	overflow: hidden;

	&:not(:last-child) {
		@include bp($point_2, min) {
			margin-bottom: 6rem;
		}
		@include bp($point_3) {
			margin-bottom: 3.6rem;
		}
	}
}

.hero__link {
	position: relative;
	display: inline-flex;

	font-weight: 300;
	color: $accent_color_1;
	letter-spacing: .042em;

	overflow: hidden;

	@include bp($point_2, min) {
		padding-bottom: .4rem;
		font-size: 4.8rem;
	}
	@include bp($point_3) {
		font-size: 2.4rem;
	}

	&:not(:first-child) {
		position: absolute;
		left: 0;
		top: 0;
	}

	&:hover,
	&:focus {
		text-decoration: none;
	}

	&::after {
		position: absolute;
		left: 0;

		width: 100%;
		height: 1px;
		background-color: currentColor;
		content: '';

		@include bp($point_2, min) {
			bottom: 0;
		}
		@include bp($point_3) {
			bottom: 4px;
		}
	}
}

.hero__button {
	@extend %default_btn;
	@extend %btn_size_1;
}

.hero__image_wrap {
	// width: 116.9%;
	// z-index: -1;
	position: absolute;
	right: 0;

	@include bp($point_2, min) {
		top: 0;
		width: 67.4vw;
		height: 0;
		margin: -9% -9.8% 0 0;
		padding-bottom: 62.2%;
	}
	@include bp($point_3) {
		top: -33vw;
		flex-shrink: 0;
    width: 100vw;
    height: 111.8vw;
	}

	// margin-right: calc((100vw - $page_width_2) / -2);
}

.hero__image_in {
	position: absolute;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
}

.hero__image {
	&.hero__image--desktop_mod {
		@include bp($point_3) {
			display: none;
		}
	}
	&.hero__image--mobile_mod {
		@include bp($point_2, min) {
			display: none;
		}
	}
}

.hero__image_img {
	@include bp($point_2, min) {
		width: 92.4%;
	}
	@include bp($point_3) {
		width: 110%;
    x: -5%;
    y: -8%;
	}
}
