.tech {
	@include bp($point_2, min) {
		padding-top: 17.1rem;
		padding-bottom: 18rem;
	}

	@include bp($point_3) {
		padding-top: 8rem;
		padding-bottom: 9rem;
	}
}

.tech__in {
	max-width: $page_width_2;
}

.tech__block {
	@extend %global_flex_block_row_wrap_flex-start;

	position: relative;

	@include bp($point_2, min) {
		padding-top: 11.8rem;
	}

	@include bp($point_3) {
		padding-top: 6rem;
	}
}

.tech__img_block {
	@include bp($point_2, min) {
		position: absolute;
		right: -5.4rem;
		top: 0;

		// width: 48.8%;
		width: 39.68vw;
	}

	@include bp($point_3) {
		min-width: 100vw;
		margin-left: calc(var(--gl-indent) * -1);

		&:not(:last-child) {
			margin-bottom: 6rem;
		}
	}
}

.tech__img {
	display: block;
	width: 100%;
}

.tech__img_in {
	display: block;
	width: 122%;
	x: -4.9%;
	y: -0.2%;
}

.tech__content {
	width: 108rem;
	max-width: 100%;
}

.tech__title {
	@extend %h0;

	@include bp($point_2, min) {
		margin-bottom: 10.6rem;
	}

	@include bp($point_3) {
		text-align: center;
		margin-left: auto;
		margin-right: auto;
		margin-bottom: 7rem;
	}
}

.tech__article {
	@include bp($point_2, min) {
		margin-bottom: 6.6rem;
	}

	@include bp($point_3) {
		margin-bottom: 4rem;
	}

	img {
		display: block;
		width: 100%;

		@include bp($point_2, min) {
			margin: 10.2rem 0 9.7rem;
		}

		@include bp($point_3) {
			margin: 5.2rem 0 5rem;
		}
	}

	h2 {
		@extend %h1;

		&:first-child {
			margin-top: 0;
		}

		@include bp($point_2, min) {
			margin: 9.6rem 0 2.4rem;
		}

		@include bp($point_3) {
			margin: 4.6rem 0 2.2rem;
		}
	}

	p {
		@extend %descrip;

		@include bp($point_2, min) {
			&.tech__paragraph {
				&:not(:last-child) {
					margin-bottom: 3.1rem;
				}
			}
		}

		@include bp($point_3) {
			&.tech__paragraph {
				&:not(:last-child) {
					margin-bottom: 2.6rem;
				}
			}
		}
	}

	a {
		color: $accent_color_1;
		letter-spacing: 0.01rem;
		font-weight: 500;
	}

	* {
		&:nth-child(-n + 8) {
			max-width: 76rem;
		}
	}

	ul {
		margin-bottom: 3rem;
	}

	li {
		@extend %descrip;

		&:before {
			content: "-";
			margin-right: 0.6rem;
		}
	}

	b,
	strong {
		font-weight: 500;
	}
}

// list

.tech_list {
	@include bp($point_2, min) {
		margin-right: clamp(-12rem, -6.56vw, -1rem);
		margin-bottom: 12.9rem;

		@include flex_block(row, wrap, flex-end);
	}

	@include bp($point_3) {
		margin-bottom: 8rem;
	}
}

.tech_list__item {
	@include bp($point_2, min) {
		padding-right: clamp(1rem, 6.56vw, 12rem);
		width: 20%;
	}

	@include bp($point_3) {
		position: relative;

		&:not(:last-child) {
			margin-bottom: 8rem;
		}
	}
}

.tech_list__item_in {
}

.tech_list__img {
	width: 100%;
	max-width: 18.3rem;
	margin-left: auto;
	margin-right: auto;
	margin-bottom: 2.6rem;

	.tech_list__item:not(:last-child) & {
		&::after{
			content: "";
			position: absolute;
			background: url("../images/right.svg") no-repeat center center;
			background-size: contain;
		}
	}

	@include bp($point_9, min) {
		.tech_list__item:not(:last-child) & {
			&::after{
				width: 3.8rem;
				height: 1.8rem;
			}
		}
	}

	@include bp($point_9 - 1) {
		.tech_list__item:not(:last-child) & {
			&::after{
				width: 2.6rem;
				height: 1.2rem;
			}
		}
	}

	@include bp($point_2, min) {
		.tech_list__item:not(:last-child) & {
			position: relative;

			&::after{
				transform: translateY(-50%);
				right: -4.9vw;
				top: calc(50% - 0.3rem);
			}
		}
	}

	@include bp($point_3) {
		.tech_list__item:not(:last-child) & {
			&::after{
				left: 50%;
				bottom: -1.5rem;
				transform: rotate(90deg) translate(100%, 100%);
			}
		}
	}
}

.tech_list__title {
	font-weight: 300;
	color: $accent_color_2;
	line-height: 1.4;
	text-align: center;
	text-transform: uppercase;

	@include bp($point_1, min) {
		font-size: 2rem;
	}

	@include bp($point_1 - 1) {
		font-size: 1.6rem;
	}
}

.tech_list__img_in {
	display: block;
	x: -3%;
}

// buttons

.tech_buttons {
	@include bp($point_2, min) {
		@include flex_block(row, wrap, flex-start);
	}

	@include bp($point_3, $point_5) {
		@include flex_block(row, wrap, center);
	}

	@include bp($point_5, min) {
		margin: 0 -5rem -3rem 0;
	}
}

.tech_buttons__item {
	@include bp($point_5, min) {
		padding: 0 5rem 3rem 0;
	}

	@include bp($point_5 - 1) {
		@include flex_block(row, wrap, center, center, center);

		&:not(:last-child) {
			margin-bottom: 3rem;
		}
	}
}

.tech_buttons__link {
	@extend %btn_size_2;
	@extend %default_btn;

	position: relative;
	min-width: 23.4rem;

	&:hover,
	&:focus{
		.tech_buttons__icon{
			color: $white;
		}
	}
}

.tech_buttons__icon {
	@extend %transition_color;

	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	right: 2.3rem;

	width: 1rem;
	height: 1.2rem;

	color: $accent_color_4
}
