.tab_panel {
	overflow: hidden;
}

.tab_panel__list {
	@extend %global_flex_block_row_wrap_flex-start;

	@include bp($point_2, min) {
		margin: 0 -10rem -3rem 0;
	}

	@include bp($point_3) {
		margin: 0 -3rem -3rem 0;
	}
}

.tab_panel__item {
	@include bp($point_2, min) {
		padding: 0 10rem 3rem 0;
	}

	@include bp($point_3) {
		padding: 0 3rem 3rem 0;
	}
}

.tab_panel__item_hidden_input {
	display: none;
}

.tab_panel__item_title {
	display: block;
	padding-bottom: 1.1rem;
	
	font-weight: 700;
	font-size: 1.6rem;
	text-transform: uppercase;
	letter-spacing: 2px;
	color: rgba($accent_color_2, .5);

	border-bottom: 2px solid transparent;
	cursor: pointer;

	transition:
		color .3s ease,
		border-bottom-color .3s ease;

	.tab_panel__item_hidden_input:checked + & {
		color: $accent_color_1;

		border-bottom-color: $accent_color_1;
		cursor: default;
	}
}