.video_section {
	@include bp($point_4, min) {
		height: 56.25vw;
	}

	@include bp($point_4 - 1) {
		//aspect-ratio: 16/ 9;
		padding-bottom: 52%;
	}
}

.video_block__in {
	position: absolute;
	top: 0;
	left: 0;

	width: 100%;
	height: 100%;

	object-fit: cover;
}

.video_section_controls {
	@extend %global_flex_block_row_wrap_center_center;
	position: absolute;
	bottom: 3.2rem;
	right: 3rem;

	background: $accent_color_1;

	@include bp($point_2, min) {
		padding: 0 2.4rem;
		width: 13rem;
		height: 6rem;
		border-radius: 4rem;
	}

	@include bp($point_3) {
		padding: 0 1.2rem;
		width: 10rem;
		height: 4rem;
		border-radius: 2rem;
	}
}

.video_section_controls__btn {
	position: relative;
	display: block;

	width: 50%;
	height: 2rem;
	cursor: pointer;

	span {
		z-index: 2;
		position: absolute;
		top: 0;
		left: 0;

		width: 100%;
		height: 100%;

		color: $white;
	}
}

.video_section_controls__btn_play {
	@extend %transition_opacity;
	.video_section_controls__btn:not(.play) & {
		opacity: 0;
	}
}

.video_section_controls__btn_pause {
	@extend %transition_opacity;
	.video_section_controls__btn.play & {
		opacity: 0;
	}
}

.video_section_controls__btn_mute {
	@extend %transition_opacity;
	.video_section_controls__btn:not(.mute) & {
		opacity: 0;
	}
}

.video_section_controls__btn_sound {
	@extend %transition_opacity;
	.video_section_controls__btn.mute & {
		opacity: 0;
	}
}

