.prodotto_listato_hero {
	padding-bottom: 5.5rem;

	@include bp($point_2, min) {
		padding-top: 28.8rem;
	}

	@include bp($point_3) {
		padding-top: 13rem;
	}
}

.prodotto_listato_hero__decor {
	position: absolute;
	top: 50%;
	left: 50%;
	z-index: -1;

	fill: $accent_color_3;

	&.prodotto_listato_hero__decor--mod_1 {
		margin: -22.6% 0 0 13.3%;

		width: 23.8vw;
		height: 26.3vw;
	}

	&.prodotto_listato_hero__decor--mod_2 {
		margin: -1.8% 0 0 29.6%;

		width: 16.6vw;
		height: 18.3vw;
	}

	&.prodotto_listato_hero__decor--mod_3 {
		margin: 2.2% 0 0 1.3%;

		width: 9.45vw;
		height: 10.4vw;
	}
}

.prodotto_listato_hero__in {
	max-width: $page_width_2;
}

.prodotto_listato_hero__body {
	max-width: 76rem;
}

.prodotto_listato_hero__title {
	@extend %h0;

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 4.5rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}
}

.prodotto_listato_hero__text {
	@extend %descrip;

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 6.1rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 3rem;
		}
	}
}

.prodotto_listato_hero__button {
	@extend %default_btn;
	@extend %btn_size_2;

	min-width: 23.4rem;

	&:hover {
		.prodotto_listato_hero__icon {
			color: $white;
		}
	}
}

.prodotto_listato_hero__icon {
	@extend %transition_color;

	position: absolute;
	top: 50%;
	right: 10%;
	transform: translateY(-50%);

	width: 1rem;
	height: 1.2rem;

	color: $accent_color_4;
}
