.footer {
	width: 100%;
	min-height: var(--height-footer);
	flex: 0 0 auto;
	padding: 0 2.2rem 4rem;

	font-size: 1rem;
	color: $accent_color_2;


	will-change: transform;

	background-color: $accent_color_3;
}

.footer__logo {
	display: block;

	@include bp($point_2, min) {
		width: 10.4rem;
		height: 11.6rem;
		margin: -5.8rem auto 6.2rem;
	}

	@include bp($point_3) {
		width: 5.2rem;
		height: 5.8rem;
		margin: -3rem auto 3.6rem;
	}
}

.footer__logo_img {
	@extend %contain_img;
}

.footer_menu {
	margin-bottom: 8rem;
}

.footer_menu__in {
	@extend %global_flex_block_row_wrap_flex-start;
}

.footer_menu__col {
	width: 11.11%;
	padding: 0 1rem;
	text-align: center;

	@include bp($point_3) {
		width: 100%;
		margin-bottom: 2.4rem;

		&:last-child {
			margin: 0;
		}
	}
}

.footer_menu__title {
	display: block;
	color: $accent_color_2;
	font-weight: 700;
	text-transform: uppercase;

	@include bp($point_2, min) {
		margin-bottom: 2rem;
		font-size: 1.4rem;
	}

	@include bp($point_3) {
		font-size: 1.2rem;
	}

	&:hover, &:focus {
		color: $accent_color_1;
		text-decoration: none;
	}
}

.footer_menu__list {
	@include bp($point_3) {
		display: none;

		&.footer_menu__list--active {
			display: block;
		}
	}
}

.footer_menu__item {
	margin-bottom: 1.4rem;

	&:last-child {
		margin: 0;
	}
}

.footer_menu__link {
	font-size: 1.4rem;
	color: $accent_color_2;
	transition: color .3s;

	&:hover, &:focus {
		color: $accent_color_1;
		text-decoration: none;
	}
}

//

.footer_social {
	@include bp($point_2, min) {
		margin-bottom: 8rem;
	}

	@include bp($point_3) {
		margin-bottom: 5.8rem;
	}
}

.footer_social__title {
	font-weight: 700;
	text-align: center;
	text-transform: uppercase;

	@include bp($point_2, min) {
		margin-bottom: 2.6rem;
		font-size: 1.4rem;
		letter-spacing: .2rem;
	}

	@include bp($point_3) {
		margin-bottom: 2rem;

		font-size: 1.2rem;
	}
}

.footer_social__list {
	@extend %global_flex_block_row_nowrap_center;
}

.footer_social__item {
	margin-right: 2.2rem;

	&:last-child {
		margin: 0;
	}
}

.footer_social__link {
	display: block;
	width: 4rem;
	height: 4rem;
	padding: 1.2rem;

	color: $white;
	background-color: $accent_color_1;
	border-radius: 50%;

	transition: background-color .3s;

	&:hover, &:focus {
		background-color: $accent_color_2;
	}
}

.footer__info {
	margin-bottom: 1.4rem;

	text-align: center;

	@include bp($point_2, min) {
		font-size: 1.4rem;
	}

	@include bp($point_3) {
		font-size: 1.2rem;
	}

	&:last-child {
		margin: 0;
	}
}

// decors

.footer_decor {
	position: absolute;
	z-index: -1;

	width: 1rem;
	height: 1rem;

	&.footer_decor--v1 {
		top: 30rem;
		left: 20.4rem;
	}

	&.footer_decor--v2 {
		bottom: 24rem;
		right: 9rem;
	}

	@include bp($point_4 - 1) {
		display: none;
	}
}

.footer_decor__in {
	position: absolute;
	top: 50%;
	left: 50%;

	z-index: -1;

	&:nth-child(1) {
		width: 22rem;
		height: 24rem;

    margin-top: -18rem;
    margin-left: -26rem;
	}

	&:nth-child(2) {
		width: 17.6rem;
		height: 19.2rem;

		margin-top: 1rem;
		margin-left: -11rem;
	}

	&:nth-child(3) {
		width: 9rem;
		height: 9.8rem;

		margin-left: 4rem;
		margin-top: -5rem;
	}
}

.footer_decor__img {
	@extend %contain_img;
}
