.application_slider {
	padding-bottom: 0;

	@include bp($point_3) {
		padding-top: 5rem;
	}
}

.application_slider__block {
	max-width: 142rem;
	margin: 0 auto;

	@include bp($point_3) {
		margin: 0 calc(var(--gl-indent) * -1) 4.2rem;
	}
}

.application_slider__slider {
	position: relative;
}

.application_slider__item {
	position: relative;

	@include bp($point_2, min) {
		@include flex_block(row, nowrap, flex-start);
		padding: 2.2rem 0;

		&:before {
			content: '';
			display: block;
			padding-bottom: 66.66%;
		}
	}
}

.application_slider__head {
	position: relative;

	width: 84.6rem;
	max-width: 100%;
	margin: 0 auto;

	z-index: 4;

	&::after{
		content:'';
		position: absolute;
		left: 50%;
		transform: translateX(-50%);

		height: 0.1rem;

		background: linear-gradient(100deg, rgba(245,245,245,1) 0%, rgba(191,191,191,1) 10%, rgba($black,1) 50%, rgba(191,191,191,1) 90%, rgba(245,245,245,1) 100%);

		@include bp($point_2, min) {
			top: -2.2rem;
			width: 76.8vw;
		}

		@include bp($point_3) {
			width: 100vw;
			opacity: 0.2;
			bottom: -2.2rem;
		}
	}

	@include bp($point_2, min) {
		@include flex_block(row, nowrap, flex-start, center, center);
	}

	@include bp($point_3) {
		padding: 0 2rem;
		margin-bottom: 2.2rem;
	}
}

.application_slider__logo {
	display: block;
	
	@include bp($point_2, min) {
		width: 18rem;
		height: 17.8rem;
		flex-shrink: 0;
		margin-right: 10rem;
	}

	@include bp($point_3) {
		width: 6.2rem;
		height: 6.2rem;
		margin: 0 auto 2rem;
	}
}

.application_slider__logo_in {
	@extend %contain_img;
}

.application_slider__text {
	font-weight: 300;
	color: $accent_color_2;
	line-height: 1.5;
	
	@include bp($point_2, min) {
		max-width: 47rem;
		margin-top: 2.6rem;

		font-size: 2rem;
	}

	@include bp($point_3) {
		font-size: 1.6rem;
		text-align: center;
	}
}

.application_slider__img_block {
	width: 100%;
	
	@include bp($point_2, min) {
		position: absolute;
		top: 0;
		left: 0;
		height: 100%;
	}

	@include bp($point_3) {
		position: relative;
	}

	&:before, &:after {
		content: '';
		position: absolute;
		left: 0;
		height: 100%;
		z-index: 2;

		pointer-events: none;
	}
	
	&:before {
		top: 0;
		width: 100%;
		height: 100%;

		background-image: linear-gradient(90deg, $accent_color_3  0%, rgba(246, 246, 246, 0) 15.6%, rgba(246, 246, 246, 0) 84.4%, $accent_color_3  100%);
	}

	&:after {
		bottom: 0;
		width: 100%;
		height: 100%;
		background-image: linear-gradient(180deg, $accent_color_3 0%, rgba(238, 238, 238, 0) 22.4%, rgba(238, 238, 238, 0) 77.6%, $accent_color_3 100%);
	}
}

.application_slider__img {
	width: 100%;
	height: 100%;
	
	@include bp($point_3) {
		position: relative;

		&:before {
			content: '';
			display: block;
			padding-bottom: 66.66%;
		}
	}
}

.application_slider__img_in {
	@extend %cover_img;

	opacity: .8;

	@include bp($point_3) {
		position: absolute;
		top: 0;
		left: 0;
	}
}

.application_slider__arrow {
	position: absolute;
	z-index: 2;

	transition: color .3s;
	cursor: pointer;

	@include bp($point_2, min) {
		top: calc(50% + 4.6rem);

		width: 3.8rem;
		height: 1.8rem;
	}

	@include bp($point_3) {
		bottom: 1.4rem;

		width: 1.8rem;
		height: 1rem;
	}

	&:hover, &:focus {
		color: $accent_color_1;
	}

	&.application_slider__arrow--prev {
		@include bp($point_2, min) {
			left: 4rem;
		}

		@include bp($point_3) {
			left: 2rem;
		}
	}

	&.application_slider__arrow--next {
		@include bp($point_2, min) {
			right: 4rem;
		}

		@include bp($point_3) {
			right: 2rem;
		}
	}
}

.application_slider__dots,
.more_info__slider_dots {
	@extend %global_flex_block_row_nowrap_flex-end;
	counter-reset: dots;

	@include bp($point_3) {
		justify-content: center;
	}

	.swiper-pagination-bullet {
		@extend %transition_opacity;
		
		position: relative;
		margin-right: 2rem;
		padding-bottom: 1rem;
		color: $accent_color_2;
		opacity: 0.3;
		
		&:last-child {
			margin: 0;
		}

		&:before {
			counter-increment: dots; 
			content: counters(dots, ".", decimal-leading-zero);

			position: relative;

			font-weight: 700;
			
			@include bp($point_2, min) {
				font-size: 1.4rem;
				letter-spacing: .2rem;

			}

			@include bp($point_3) {
				font-size: 1.2rem;
				letter-spacing: .17rem;
			}
		}

		&.swiper-pagination-bullet-active {
			opacity: 1;

			&:after {
				content: '';
				position: absolute;
				left: 0;

				height: 2px;
				width: 100%;

				background-color: $accent_color_1;

				@include bp($point_2, min) {
					bottom: 0;
				}

				@include bp($point_3) {
					bottom: 0.9rem;
				}
			}
		}
	}
}