.sustainability {
	@include bp($point_2, min) {
		padding-top: 2.2rem;
		padding-bottom: 1.7rem;
	}

	@include bp($point_3) {
		padding: 3.5rem 0.8rem 4.2rem;
	}
}

.sustainability__in {
	max-width: $page_width_2;
}

.sustainability__row {
	@include bp($point_1, min) {
		margin-right: -12rem;
	}

	@include bp($point_1 - 1, $point_2) {
		margin-right: -6rem;
	}

	@include bp($point_2, min) {
		@include flex_block(row, nowrap, flex-start, center, center);
	}
}

.sustainability__column {
	@include bp($point_1, min) {
		padding-right: 12rem;
	}

	@include bp($point_1 - 1, $point_2) {
		padding-right: 6rem;
	}

	@include bp($point_2, min) {
		width: 50%;

		&:first-child {
			padding-bottom: 40vw;
		}
	}

	@include bp($point_3) {
		text-align: center;

		&:not(:last-child) {
			margin-bottom: 1.6rem;
		}
	}

	@include bp($point_3, $point_5) {
		&:first-child {
			height: 119vw;
		}
	}

	@include bp($point_5 - 1) {
		&:first-child {
			height: 200vw;
		}
	}
}

.sustainability_image {
	position: absolute;

	@include bp($point_2, min) {
		top: 0;
		left: -0.1rem;

		width: 42.2%;
		height: 100%;
	}

	@include bp($point_3) {
		left: calc(50% - 0.1rem);
		transform: translateX(-50%);
	}

	@include bp($point_3, $point_5) {
		top: -55vw;

		width: 203vw;
		height: 235.3vw;
	}

	@include bp($point_5 - 1) {
		top: -94vw;

		width: 345vw;
		height: 400vw;
	}
}

.sustainability_image__in {
	position: absolute;
	top: 0;
	left: 0;

	&.sustainability_image__in--desktop_mod {
		@include bp($point_3) {
			display: none;
		}
	}

	&.sustainability_image__in--mobile_mod {
		@include bp($point_2, min) {
			display: none;
		}
	}
}

.sustainability_image__wrap {
	transform: translateX(-6%);
}

.sustainability_image__img {
	@include bp($point_3, $point_5) {
		width: 60%;
	}

	@include bp($point_3) {
		x: 16.3%;
		y: 22.7%;
	}
}

.sustainability__title {
	@extend %h1;
	max-width: var(--width-content);

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 1.2rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin: 0 auto 1.4rem;
		}
	}
}

.sustainability__descr {
	@extend %descrip;
	max-width: var(--width-content);

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 9.8rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin: 0 auto 6.3rem;
		}
	}
}

.sustainability__button {
	@extend %btn_size_1;
	@extend %default_btn;
	letter-spacing: 0.2rem;
}
