.grants--mod_1 {
	@include bp($point_2, min) {
		padding-top: 29.1rem;
		padding-bottom: 16rem;
	}

	@include bp($point_3) {
		padding-top: 13rem;
		padding-bottom: 8rem;
	}
}

.grants--mod_2 {
	background-color: $accent_color_3;

	@include bp($point_2, min) {
		padding-top: 20.3rem;
		padding-bottom: 23.2rem;
	}

	@include bp($point_3) {
		padding-top: 10.3rem;
		padding-bottom: 13.2rem;
	}
}

.grants--mod_3 {
	@include bp($point_2, min) {
		padding-bottom: 10.6rem;
	}

	@include bp($point_3) {
		padding-bottom: 8.6rem;
	}
}

.grants--mod_4 {
	background-color: $accent_color_3;

	@include bp($point_2, min) {
		padding-top: 20.2rem;
		padding-bottom: 24.8rem;
	}

	@include bp($point_3) {
		padding-top: 10.2rem;
		padding-bottom: 14.6rem;
	}
}

.grants__decor {
	position: absolute;
	z-index: 2;
	top: 50%;
	left: 50%;

	fill: $accent_color_3;

	&.grants__decor--mod_1 {
		margin: -22.1% 0 0 13.3%;

		width: 23.8vw;
		height: 26.2vw;
	}

	&.grants__decor--mod_2 {
		margin: -1.3% 0 0 29.6%;

		width: 16.6vw;
		height: 18.3vw;
	}

	&.grants__decor--mod_3 {
		margin: 2.7% 0 0 1.2%;

		width: 9.45vw;
		height: 10.5vw;
	}
}

.grants_bg {
	position: absolute;
	left: 0;
	z-index: 1;

	width: 100%;
	background-color: $white;

	&.grants_bg--top {
		top: 0;
		height: 5.36vw;

		.app_list_sliders__block_w:nth-child(4n + 3) & {
			transform: rotateY(180deg);
		}
	}

	&.grants_bg--bottom {
		bottom: 0;
		height: 6.1vw;

		.app_list_sliders__block_w:nth-child(4n + 3) & {
			transform: rotateY(180deg);
		}
	}
}

.grants__in {
	position: relative;
	z-index: 3;

	max-width: $page_width_2;
}

.grants__main_title {
	@extend %h0;

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 4.3rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 2.8rem;
		}
	}
}

.grants__title {
	@extend %h1;
	text-transform: uppercase;

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 1.8rem;
		}
	}
}

.grants__content {
	&.grants__content--enabled {
		overflow: hidden;
		transition: all 0.25s ease-out;
		will-change: max-height;
	}
}

.grants_text {
	@extend %descrip;

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 4rem;
		}

		p:not(:last-child) {
			margin-bottom: 3rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child),
		p:not(:last-child) {
			margin-bottom: 2.6rem;
		}
	}
}

.grants_text--mod_1 {
	max-width: 76rem;
}

.grants_image {
	display: inline-block;

	&:not(:last-child) {
		margin-bottom: 5.9rem;
	}
}

.grants_image__in {
	@extend %contain_img;
}

.grants__button {
	@extend %transition_opacity;

	position: relative;

	text-transform: uppercase;
	color: $accent_color_1;
	font-weight: 700;
	cursor: pointer;

	&::before {
		@extend %transition_transform;

		content: "";
		position: absolute;
		bottom: 0;
		left: 0;

		width: 100%;
		height: 0.1rem;
		background-color: $accent_color_1;
	}

	&:hover,
	&:focus {
		&::before {
			transform: scaleX(0);
		}
	}

	@include bp($point_2, min) {
		font-size: 1.8rem;
	}

	@include bp($point_3) {
		font-size: 1.4rem;
	}
}

.grants__button--less {
	display: block;
	opacity: 0;

	.grants__content--active_state & {
		opacity: 1;
	}
}

.grants__button--more {
	.grants__content--active_state & {
		opacity: 0;
	}
}
