.applications_accordion_ {
	margin: 0 -2rem;
	
	@include bp($point_1, min) {
		display: none;
	}
}

.icon {
	&.size-mod {
		display: block;
		width: 100%;
		height: 100%;
	}
}

.applications_accordion__item {
	display: block;
}

.applications_accordion__item_head {
	@extend %global_flex_block_row_wrap_flex-start_center;

	height: 7rem;

	padding: 0 2rem;

	border-bottom: 1px solid rgba($accent_color_2, .2);
	cursor: pointer;

	.applications_accordion__item:first-child & {
		border-top: 1px solid rgba($accent_color_2, .2);
	}

	//transition: border-color .3s;

	.applications_accordion__item_icon_w {
		display: block;
		margin: 0 0 0 auto;

		width: .9rem;
		height: .5rem;

		color: $accent_color_2;

		//transition: border-color .3s;

		transform-origin: center;
		transition: transform .3s, color .3s;
		transform: rotateZ(0deg);

		.applications_accordion__item--active-mod & {
			color: $accent_color_1;
			transform: rotateZ(180deg);
		}
	}

	.applications_accordion__item--active-mod.accordion__item:first-child & {
		border-top: 1px solid rgba($accent_color_2, .2);
		border-bottom: none;
	}

	.applications_accordion__item--active-mod & {
		border-bottom: none;
	}
}

.applications_accordion__item_title {
	font-size: 2.4rem;
	font-weight: 300;
	color: $accent_color_2;

	.applications_accordion__item--active-mod & {
		color: $accent_color_1;
	}
}

.applications_accordion__item_body {
	color: $accent_color_2;
	font-size: 1.6rem;
	font-weight: 400;

	padding: 0 1.6rem;

	max-height: 0;
	opacity: 0;

	transition: all 0.25s ease-out;
	will-change: max-height;
	overflow: hidden;
	pointer-events: none;

	.applications_accordion__item--active-mod & {
		opacity: 1;
		pointer-events: auto;
	}
}

.applications_accordion__content_list {
	@extend %global_flex_block_row_nowrap_flex-start;

	width: 100%;

	padding-top: 1rem !important;
	margin-bottom: 1rem;
}

.applications_accordion__content_list_item_w {
	width: 28rem !important;
	height: 37.6rem !important;

	margin-right: 1rem;
}

.applications_accordion__content_list_item {
	position: relative;

	flex-shrink: 0;

	width: 28rem;
	height: 30.6rem;

	margin-right: 1rem;

	text-align: center;
}

.app_scrollbar {
	position: relative;

  height: 2rem;
  width: 100%;

	margin: 0 0 3rem;

  cursor: pointer;

  z-index: 3;

	&::after {
		z-index: -1;
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    height: 1px;
    background-color: #d8d8d8;
    content: "";
	}

	& .swiper-scrollbar-drag {
		background: url(../images/decor_scrollbar.svg) no-repeat center center;
	}
}