.investors_item {
	display: block;
	height: 100%;
	max-width: 75.4rem;

	background-color: $white;
	box-shadow: 0 0.2rem 1.2rem rgba($black, 0.15);
	transition: box-shadow 0.3s ease 0s;
	will-change: box-shadow;

	&:hover,
	&:focus {
		text-decoration: none;
		box-shadow: none;
	
		.investors_item_bg,
		.investors_item__text {
			color: $accent_color_1;
		}

		.investors_item__date{
			color: $white;
		}
	}

	@include bp($point_1, min) {
		padding: 3.4rem;
	}

	@include bp($point_1 - 1, $point_5) {
		padding: 2.4rem;
	}

	@include bp($point_3) {
		margin: 0 auto;
	}

	@include bp($point_5 - 1) {
		padding: 1.4rem;
	}

	@include flex_block(column, nowrap, center);
}

.investors_item__row {
	@include bp($point_1, min) {
		margin-right: -5rem;
	}

	@include bp($point_1 - 1, $point_5) {
		margin-right: -2rem;
	}

	@include bp($point_5, min) {
		@include flex_block(row, nowrap, flex-start, center, center);
	}

	@include bp($point_5 - 1) {
		@include flex_block(column, nowrap, center, center, center);
		margin: 0 auto;
		text-align: center;
	}
}

.investors_item__column {
	@include bp($point_1, min) {
		padding-right: 5rem;
	}

	@include bp($point_1 - 1, $point_5) {
		padding-right: 2rem;
	}

	@include bp($point_5 - 1) {
		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}
}

.investors_item__date {
	@extend %transition_color;

	position: relative;
	z-index: 2;

	color: $accent_color_1;
	font-weight: 300;
	text-align: center;

	@include bp($point_1, min) {
		font-size: 3.6rem;
		padding: 8.3rem 2.4rem 7.2rem;
	}

	@include bp($point_1 - 1) {
		font-size: 2.8rem;
		padding: 6rem 1.4rem 5.2rem;
	}
}

.investors_item_bg {
	@extend %transition_color;

	position: absolute;
	top: 0;
	left: 0;
	z-index: -1;

	width: 100%;
	height: 100%;

	fill: currentColor;
	color: $accent_color_3;
}

.investors_item__text {
	@extend %transition_color;
	@extend %descrip;

	color: $accent_color_2;
}
