.applicazioni_prodotto_hero {
	z-index: 2;

	padding-bottom: 2.8rem;

	@include bp($point_2, min) {
		padding-top: 17.3rem;
	}

	@include bp($point_3) {
		padding-top: 10rem;
	}
}

.applicazioni_prodotto_hero__in {
	max-width: $page_width_2;
}

.applicazioni_prodotto_hero__row {
	@include flex_block(row, wrap, flex-start);
}

.applicazioni_prodotto_hero__column {
	&:first-child {
		@include bp($point_2, min) {
			width: 53%;
			padding: 1.2rem 6rem 0 0;
		}

		@include bp($point_3) {
			margin-bottom: 3rem;
			width: 100%;
		}
	}

	&:last-child {
		@include bp($point_2, min) {
			width: 47%;
		}

		@include bp($point_3) {
			width: 100%;
		}
	}
}

.applicazioni_prodotto_hero__link {
	position: relative;
	height: 5.8rem;
	width: 5.2rem;

	margin-left: 0.1rem;

	&:hover,
	&:focus {
		.applicazioni_prodotto_hero__decor {
			color: $accent_color_1;
		}

		.applicazioni_prodotto_hero__icon {
			color: $white;
		}
	}

	@include flex_block(row, nowrap, center, center, center);

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 4.5rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 3rem;
		}
	}
}

.applicazioni_prodotto_hero__decor {
	@extend %transition_color;

	position: absolute;
	top: 0;
	left: 0;

	width: 100%;
	height: 100%;

	color: $accent_color_3;

	svg {
		display: block;
		width: 100%;
		height: 100%;
		fill: currentColor;
	}
}

.applicazioni_prodotto_hero__icon {
	@extend %transition_color;

	color: $accent_color_4;
	width: 1.8rem;
	height: 1rem;
	transform: rotate(-180deg);
}

.applicazioni_prodotto_hero__title {
	@extend %h0;

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 4.5rem;
		}
	}

	@include bp($point_3) {
		text-align: center;

		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}
}

.applicazioni_prodotto_hero__text {
	@extend %descrip;

	@include bp($point_3) {
		text-align: center;
	}
}

.applicazioni_prodotto_hero__img_block {
	@include bp($point_2, min) {
		width: calc(100% + 18.7rem);
		margin-left: -2.7rem;
	}

	@include bp($point_3) {
		margin: 0 calc(var(--gl-indent) * -1);
	}
}

.applicazioni_prodotto_hero__img {
	display: block;
	width: 100%;
}

.applicazioni_prodotto_hero__img_in {
	y: -17.4%;
	x: -2.2%;
}
