.investors {
	@include bp($point_2, min) {
		padding-top: 29.1rem;
		padding-bottom: 23.7rem;
	}

	@include bp($point_3) {
		padding-top: 13rem;
		padding-bottom: 15rem;
	}
}

.investors__decor {
	position: absolute;
	top: 50%;
	left: 50%;
	z-index: -1;

	fill: $accent_color_3;

	&.investors__decor--mod_1 {
		margin: -45.6% 0 0 20.1%;

		width: 23.8vw;
		height: 26.3vw;
	}

	&.investors__decor--mod_2 {
		margin: -24.7% 0 0 36.4%;

		width: 16.6vw;
		height: 18.3vw;
	}

	&.investors__decor--mod_3 {
		margin: -51.4% 0 0 39.4%;

		width: 9.45vw;
		height: 10.4vw;
	}
}

.investors__in {
	max-width: $page_width_2;
}

.investors__head {
	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 9.2rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 5rem;
		}
	}
}

.investors__title {
	@extend %h0;

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 4.3rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 2.8rem;
		}
	}
}

.investors__descr {
	@extend %descrip;
}

.investors_body {
	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 7rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 4.4rem;
		}
	}
}

.investors_body__nav {
	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 6rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 3.8rem;
		}
	}
}

.investors_body__list {
	@include flex_block(row, wrap, center);

	@include bp($point_2, min) {
		margin: 0 -10rem -2rem 0;
	}

	@include bp($point_3) {
		margin: 0 -5rem -2rem 0;
	}
}

.investors_body__item {
	@include bp($point_2, min) {
		padding: 0 10rem 2rem 0;
	}

	@include bp($point_3) {
		padding: 0 5rem 2rem 0;
	}
}

.investors_body__title {
	opacity: 0.5;
	color: $accent_color_2;
	font-weight: 700;
	letter-spacing: 0.1rem;
	text-align: center;
	text-transform: uppercase;
	font-family: $roboto_font;

	padding-bottom: 1.2rem;
	border-bottom: 0.2rem solid transparent;
	cursor: pointer;

	transition: color 0.3s ease 0s, opacity 0.3s ease 0s, border-color 0.3s ease 0s;

	&.investors_body__title--active_state {
		color: $accent_color_1;
		opacity: 1;
		border-color: $accent_color_1;
	}

	@include bp($point_5, min) {
		font-size: 1.6rem;
	}

	@include bp($point_5 - 1) {
		font-size: 1.4rem;
	}
}

.investors_body_content {
	display: none;

	&.investors_body_content--active_state {
		display: block;
	}
}

.investors_body_content__list {
	@include bp($point_2, min) {
		margin: 0 -4rem -3rem 0;

		@include flex_block(row, wrap, flex-start, stretch);
	}
}

.investors_body_content__item {
	@include bp($point_2, min) {
		padding: 0 4rem 3rem 0;

		flex-shrink: 0;
		width: 50%;
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 3rem;
		}
	}
}

.investors__footer {
	@include flex_block(row, nowrap, center);
}

.investors__button {
	@extend %btn_size_1;
	@extend %default_btn;

	@include bp($point_2, min) {
		min-width: 27.4rem;
	}
}
