%form_el_label {
	position: absolute;
	top: 1rem;

	display: block;

	font-size: 1.6rem;
	color: $accent_color_2;
	opacity: 0.35;
	transform-origin: 0% 0%;
	transition: transform .3s;

	.form_input--active_state & {
		transform: scale(0.75) translateY(-2.4rem);
	}
}

%form_el_default {
	display: block;
	width: 100%;
	height: $input_height;
	padding: $input_offset;
	font-size: $input_font_size;
	font-family: $roboto_font;
	color: $input_text_color;
	border: none;
	border-bottom: 1px solid rgba($input_border_color, 0.4);
	border-radius: 0;

	&::-webkit-input-placeholder {
		color: $input_placeholder_color;
	}

	&::-moz-placeholder {
		color: $input_placeholder_color;
	}

	&:-ms-input-placeholder {
		color: $input_placeholder_color;
	}

	&:hover,
	&:focus {
		border-color: $accent_color_1;
	}
}

.request__btn {
	@extend %default_btn;
	@extend %btn_size_1;
}