.storyline {
	--storyline_bg: #{$white};
	
	@extend %global_flex_block_row_nowrap_flex-start;

	position: relative;

	background-color: var(--storyline_bg);

	user-select: none;
}

.storyline__line {
	@extend %global_flex_block_row_nowrap_flex-start;

	flex-shrink: 0;

	cursor: grab;

	&::before {
		position: absolute;
		left: 0;

		display: block;
		margin-bottom: -1px;
		height: 2px;
		width: 100%;

		background-color: #b2b2b1;

		content: '';

		@include bp($point_4, min) {
			bottom: 50%;
		}

		@include bp($point_4 - 1) {
			bottom: 10.1rem;
		}
	}
}

.storyline__point {
	@extend %global_flex_block_column_nowrap_flex-start;

	position: relative;

	&:not(:last-child) {
		margin-right: .8rem;
	}

	&.storyline__point--reversed_mod {
		@include bp($point_4, min) {
			flex-direction: column-reverse;
		}
	}
}

.storyline__point_event_list {
	margin: 0 0 4px 9.1rem;	
	padding-left: 2.3rem;
	min-height: 23rem;

	border-left: 1px solid $accent_color_1;

	@include bp($point_4, min) {
		max-width: 49.5rem;
	}

	@include bp($point_4 - 1) {
		max-width: 20rem;
	}

	.storyline__point:not(:first-child) & {
		margin-left: 10.5rem
	}

	.storyline__point--reversed_mod & {
		@include bp($point_4, min) {			
			margin-top: 4px;
			margin-bottom: 0;

			@include flex_block(column, nowrap, flex-end);
		}
	}
}

.storyline__point_event {
	&:first-child {
		padding-top: 1px;
	}

	&:not(:last-child) {
		margin-bottom: 1.6rem;
	}

	&:last-child {
		padding-bottom: 1px;
	}
}

.storyline__point_event_title {
	font-weight: 500;
	font-size: 2rem;
	color: $accent_color_1;
}

.storyline__point_event_description {
	font-weight: 300;
	font-size: 1.4rem;
	color: $accent_color_2;
}

.storyline__point_hexagon_w {
	padding: 0 1.4rem;

	background-color: var(--storyline_bg);

	.storyline__point:first-child & {
		padding-left: 0;
	}

	.storyline__point:last-child & {
		width: 100%;
	}
}

.storyline__point_hexagon {
	@extend %global_flex_block_row_nowrap_center_center;

	width: 18.3rem;
	height: 20.3rem;

	font-weight: 300;
	font-size: 4.8rem;
	text-align: center;
	color: $accent_color_1;

	background-size: 100% 100%;
}