.prodotto_scheda_hero {
	@include bp($point_2, min) {
		padding-top: 17.2rem;
		padding-bottom: 13.5rem;
	}

	@include bp($point_3) {
		padding-top: 10rem;
		padding-bottom: 7rem;
	}
}

.prodotto_scheda_hero__in {
	max-width: $page_width_2;
}

.prodotto_scheda_hero__row {
	@include flex_block(row, wrap, flex-start);

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 4.9rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 3rem;
		}
	}
}

.prodotto_scheda_hero__column {
	&:first-child {
		@include bp($point_1 + 1, min) {
			padding: 1.3rem 6.4rem 0 0;
			width: 53%;
		}

		@include bp($point_1, $point_2) {
			padding: 1.3rem 2rem 0 0;
			width: 60%;
		}

		@include bp($point_3) {
			width: 100%;
			margin-bottom: 3rem;
		}
	}

	&:last-child {
		@include bp($point_1 + 1, min) {
			width: 47%;
		}

		@include bp($point_1, $point_2) {
			width: 40%;
		}

		@include bp($point_3) {
			width: 100%;
		}
	}
}

.prodotto_scheda_hero__link {
	position: relative;
	height: 5.8rem;
	width: 5.2rem;

	margin-left: 0.1rem;

	&:hover,
	&:focus {
		.prodotto_scheda_hero__decor {
			color: $accent_color_1;
		}

		.prodotto_scheda_hero__icon {
			color: $white;
		}
	}

	@include flex_block(row, nowrap, center, center, center);

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 4.5rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 3rem;
		}
	}
}

.prodotto_scheda_hero__decor {
	@extend %transition_color;

	position: absolute;
	top: 0;
	left: 0;

	width: 100%;
	height: 100%;

	color: $accent_color_3;

	svg {
		display: block;
		width: 100%;
		height: 100%;
		fill: currentColor;
	}
}

.prodotto_scheda_hero__icon {
	@extend %transition_color;

	color: $accent_color_4;
	width: 1.8rem;
	height: 1rem;
	transform: rotate(-180deg);
}

.prodotto_scheda_hero__title {
	@extend %h0;

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 4.5rem;
		}
	}

	@include bp($point_3) {
		text-align: center;

		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}
}

.prodotto_scheda_hero__info {
	@extend %descrip;

	li {
		&::before {
			content: "-";
			display: block;
		}

		@include flex_block(row, nowrap, flex-start, flex-start, center);

		@include bp($point_2, min) {
			&::before {
				margin-right: 2.2rem;
			}
		}

		@include bp($point_3) {
			&::before {
				margin-right: 1.4rem;
			}
		}
	}

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 8.5rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 4rem;
		}
	}
}

.prodotto_scheda_hero__list {
	margin: 0 -4rem -4rem 0;

	@include bp($point_11 + 1, min) {
		padding: 0 0.2rem;
	}

	@include bp($point_11, $point_2) {
		padding: 0 2.2rem;
	}

	@include bp($point_2, min) {
		@include flex_block(row, wrap, flex-start, center, center);
	}

	@include bp($point_3) {
		@include flex_block(row, wrap, center, center, center);
	}
}

.prodotto_scheda_hero_item {
	padding: 0 4rem 4rem 0;

	@include bp($point_3, $point_5 + 1) {
		width: 33.333%;
	}

	@include bp($point_5) {
		width: 50%;
	}
}

.prodotto_scheda_hero_item__in {
	@include bp($point_2, min) {
		position: relative;

		&:hover,
		&:focus {
			.prodotto_scheda_hero_item__icon {
				color: $accent_color_1;
			}

			.prodotto_scheda_hero_item__text {
				opacity: 1;
				pointer-events: all;
			}
		}
	}
}

.prodotto_scheda_hero_item__icon {
	@extend %transition_color;

	width: 5rem;
	height: 5.2rem;

	@include bp($point_2, min) {
		color: $accent_color_5;
	}

	@include bp($point_3) {
		color: $accent_color_1;
		margin-left: auto;
		margin-right: auto;

		&:not(:last-child) {
			margin-bottom: 1rem;
		}
	}
}

.prodotto_scheda_hero_item__text {
	color: $accent_color_1;
	font-size: 1.4rem;
	letter-spacing: -0.01rem;
	font-weight: 700;
	text-transform: uppercase;

	@include bp($point_2, min) {
		position: absolute;
		top: -0.5rem;
		left: 50%;
		transform: translate(-50%, -100%);
		z-index: 1;

		padding: 1.8rem 0.6rem 1.4rem 1.3rem;
		width: 15.3rem;

		background-color: $white;
		box-shadow: 0 0.2rem 1.2rem rgba($black, 0.15);

		opacity: 0;
		pointer-events: none;
		transition: opacity 0.3s ease 0s;
		will-change: opacity;

		&::after {
			content: "";
			position: absolute;
			bottom: 0;
			left: 50%;
			transform: translate(-50%, 100%);

			width: 0;
			height: 0;
			border-left: 1rem solid transparent;
			border-right: 1rem solid transparent;
			border-top: 1rem solid $white;
		}
	}

	@include bp($point_3) {
		text-align: center;
	}
}

.prodotto_scheda_hero__img_block {
	@include bp($point_1, min) {
		margin-left: 2.6rem;
	}

	@include bp($point_2, min) {
		width: calc(100% + 2.8rem);
	}

	@include bp($point_3) {
		margin: 0 calc(var(--gl-indent) * -1);
	}
}

.prodotto_scheda_hero__img {
	display: block;
	width: 100%;
}

.prodotto_scheda_hero__img_in {
	x: -11.8%;
	y: 1.7%;
}

.prodotto_scheda_hero__content {
	max-width: 92rem;
}

.prodotto_scheda_hero_block {
	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 6rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 3rem;
		}
	}
}

.prodotto_scheda_hero_block__title {
	@extend %h1;

	&:not(:last-child) {
		margin-bottom: 1.5rem;
	}
}

.prodotto_scheda_hero_block__text {
	@extend %descrip;

	li {
		&::before {
			content: "-";
			display: block;
		}

		@include flex_block(row, nowrap, flex-start, flex-start, center);

		@include bp($point_2, min) {
			&::before {
				margin-right: 2.2rem;
			}
		}

		@include bp($point_3) {
			&::before {
				margin-right: 1.4rem;
			}
		}
	}

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 8.2rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 4.1rem;
		}
	}
}

.prodotto_scheda_hero_block__buttons {
	@include bp($point_2, min) {
		@include flex_block(row, wrap, flex-start);
	}

	@include bp($point_3, $point_5) {
		@include flex_block(row, wrap, center);
	}

	@include bp($point_5, min) {
		margin: 0 -5rem -3rem 0;
	}
}

.prodotto_scheda_hero_block__item {
	@include bp($point_5, min) {
		padding: 0 5rem 3rem 0;
	}

	@include bp($point_5 - 1) {
		@include flex_block(row, wrap, center, center, center);

		&:not(:last-child) {
			margin-bottom: 3rem;
		}
	}
}

.prodotto_scheda_hero_block__link {
	@extend %btn_size_2;
	@extend %default_btn;

	position: relative;
	min-width: 23.4rem;

	&:hover,
	&:focus {
		.prodotto_scheda_hero_block__icon {
			color: $white;
		}
	}
}

.prodotto_scheda_hero_block__icon {
	@extend %transition_color;

	position: absolute;
	top: 50%;
	transform: translateY(-50%);
	right: 2.3rem;

	width: 1rem;
	height: 1.2rem;

	color: $accent_color_4;

	&.prodotto_scheda_hero_block__icon--mod_1 {
		transform: translateY(-50%) rotate(-90deg);
	}
}
