//------------------------------------------------------------base styles
:root {
	--full-screen: calc(var(--vh, 1vh) * 100);

	--height-header: 8.4rem;
	--height-footer: 10rem;

	@include bp($point_2, min) {
		--gl-indent: 3.8rem;
	}

	@include bp($point_3) {
		--gl-indent: 2rem;
		--width-content: clamp(43rem, 38vw, 60rem);
	}
}

html {
	height: 100%;

	font-family: $roboto_font;
	font-size: 10px;
	font-weight: 400;
	line-height: 1.33;

	font-size: 10px !important;

	@include bp(1921px, min) {
		font-size: calc(100vw / 1920 * 10);
	}
}

body {
	position: relative;

	display: flex;
	height: 100%;

	font-size: $font_size_base;
	color: $color_text_base;

	-webkit-text-size-adjust: none;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	background: $body_bg;

	@supports (display: grid) {
		display: block;
	}

	&.body--menu_active_state,
	&.body--popup_open {
		overflow: hidden;
	}
}

.wrapper {
	position: relative;

	width: 100%;
	display: flex;
	flex-direction: column;
	min-height: 100%;
	height: auto !important;

	overflow-x: hidden;
}

.base {
	width: 100%;
	position: relative;
	// padding: var(--height-header) 0 0;

	flex: 1 0 auto;
}

a {
	text-decoration: none;
	color: $color_link_base;

	&:hover,
	&:focus {
		text-decoration: underline;
	}
}

//------------------------------------------------------------base styles###
