.more_info {
	background-color: $accent_color_3;
	
	@include bp($point_2, min) {
		padding-top: 28.4rem;
		padding-bottom: 36rem;
	}
	
	@include bp($point_3) {
		padding-top: 13.4rem;
		padding-bottom: 24rem;
	}
}

.more_info__section_in {
	max-width: $page_width_2;
}

.more_info__title {
	@extend %h1;

	margin-bottom: 6.2rem;
}

.more_info__slider_w {
	position: relative;
	
	@include bp($point_2, min) {
		padding: 0 5rem;
	}
}

.more_info__slider {
	max-width: $page_width_2;
}

.more_info__slider_item {
	@include flex_block(row, wrap, flex-start, center, stretch);

	@include bp($point_3) {
		flex-direction: column;
	}
}

.more_info__slider_img_w {
	@include bp($point_9, min) {
		width: 62.8rem;
	}

	@media screen and (max-width: $point_9 - 1) and (min-width: $point_2) {
		width: 41.8rem;
	}

	@media screen and (max-width: $point_3) and (min-width: $point_5) {
		width: 41.8rem;
		margin-bottom: 6rem;
	}

	@include bp($point_5 - 1) {
		width: 100%;
		margin-bottom: 5rem;
	}
}

.more_info__slider_img {
	position: absolute;
	top: 0;
	left: 0;
	
	display: block;

	width: 100%;
	height: 100%;
}

.more_info__slider_img_in {
	@extend %cover_img;
}

.more_info__slider_text_w {
	@include bp($point_2, min) {
		max-width: 50.6%;

		margin-top: -5rem;
		margin-left: auto;
		padding-left: 3rem;
	}

	@include bp($point_3) {
		margin-top: 0;
	}
	
	&.more_info__slider_text_w--mod_1 {
		max-width: 100%;

		margin-top: 0;
	
		text-align: center;
		
		@include bp($point_9, min) {
			padding: 23rem 16rem 0;
		}

		@media screen and (max-width: $point_9 - 1) and (min-width: $point_2) {
			padding: 5rem 16rem 0;
		}
	}
}

.more_info__slider_text {
	font-weight: 300;
	line-height: 5rem;

	@include bp($point_9, min) {
		font-size: 3.6rem;
	}

	@media screen and (max-width: $point_9 - 1) and (min-width: $point_2) {
		font-size: 2.8rem;
	}

	@include bp($point_3) {
		font-size: 2rem;
		line-height: 3rem;
		text-align: center;
	}
}

.more_info__slider_arrow {
	position: absolute;
	z-index: 2;
	
	width: 3.8rem;
	height: 1.8rem;

	@include bp($point_9, min) {
		top: 35.2rem;
	}

	@media screen and (max-width: $point_9 - 1) and (min-width: $point_2) {
		top: 22rem;
	}

	@include bp($point_3) {
		display: none;
	}

	&.more_info__slider_arrow--prev {
		left: 0;
	}

	&.more_info__slider_arrow--next {
		right: 0;
	}
}

.more_info__slider_dots {
	position: absolute;
	top: calc(100% + 9.6rem);
	right: 1.6rem;
}