@mixin st_btn() {
	display: inline-flex;
	justify-content: center;
	align-items: center;

	text-align: center;
	white-space: nowrap;

	cursor: pointer;

	&:hover, &:focus {
		text-decoration: none;
	}

}

//color extends
%default_btn {
	position: relative;
	overflow: hidden;
	font-weight: 700;
	text-transform: uppercase;
	color: $accent_color_2;
	letter-spacing: .14em;

	background-color: transparent;
	transition: all .3s ease;

	@include st_btn();

	&:after {
		@extend %transition_transform;
		z-index: -1;
		position: absolute;
		top: 0;
		left: 0;

		display: block;
		width: 100%;
		height: 100%;
		background: $accent_color_1;

		transform: translateX(-98.6%);

		content: '';
	}

	&:before {
		@extend %transition_opacity;
		position: absolute;
		top: 0;
		left: 0;

		display: block;
		width: 100%;
		height: 100%;

		border: 1px solid $accent_color_4;
		content: '';
	}

	&:hover {
		color: white;

		&:after {
			transform: translateX(0);
		}

		&:before {
			border-color: $accent_color_1;
			// opacity: 0;
		}
	}
}

%white_btn {
	@extend %default_btn;

	background-color: $white;

	&::before {
		content: none;
	}
}

//size extends
%btn_size_1 {
	padding: 0 1.2rem;

	@include bp($point_2, min) {
		height: 7rem;
		min-width: 22rem;
		line-height: 7rem;
		font-size: 1.4rem;
	}

	@include bp($point_3) {
		height: 5rem;
		min-width: 16rem;
		line-height: 5rem;
		font-size: 1.2rem;
	}
}

%btn_size_2 {
	padding: 0 4rem;

	@include bp($point_2, min) {
		height: 7rem;
		min-width: 22rem;
		line-height: 7rem;
		font-size: 1.4rem;
	}

	@include bp($point_3) {
		height: 5rem;
		min-width: 16rem;
		line-height: 5rem;
		font-size: 1.2rem;
	}
}