.request{
	@include bp($point_2, min) {
		padding-top: 5.3rem;
		padding-bottom: 24.9rem;
	}
}

.request__in {
	max-width: $page_width_2;
}

.request__block {
	max-width: 76rem;
}

.request__title {
	@extend %h1;

	margin-bottom: 5.4rem;
}

.request__list_wrap {
	margin-bottom: 3.6rem;
}

.request__item {
	margin-bottom: 2.7rem;

	&:last-child {
		margin: 0;
	}
}

.request__checkbox {
	margin-bottom: 8rem;
}