.applicazioni_listato_hero {
	padding-top: 17.3rem;
	padding-bottom: 2.8rem;
}

.applicazioni_listato_hero__in {
	max-width: $page_width_2;
}

.applicazioni_listato_hero__row {
	@include flex_block(row, wrap, flex-start);

	position: relative;
	z-index: 2;
}

.applicazioni_listato_hero__column {
	&:first-child {
		@include bp($point_2, min) {
			width: 53%;
			padding: 11.6rem 6rem 0 0;
		}

		@include bp($point_3) {
			order: 2;
			width: 100%;
		}
	}

	&:last-child {
		@include bp($point_2, min) {
			width: 47%;
		}

		@include bp($point_3) {
			width: 100%;
			margin-bottom: 3rem;
		}
	}
}

.applicazioni_listato_hero__title {
	@extend %h0;

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 4.4rem;
		}
	}

	@include bp($point_3) {
		text-align: center;

		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}
}

.applicazioni_listato_hero__text {
	@extend %descrip;

	strong {
		display: block;

		color: $accent_color_1;
		font-weight: 700;
	}

	&:not(:last-child) {
		margin-bottom: 3.1rem;
	}

	@include bp($point_3) {
		text-align: center;
	}
}

.applicazioni_listato_hero__wrap {
	@include bp($point_3) {
		@include flex_block(row, nowrap, center);
	}
}

.applicazioni_listato_hero__button {
	@extend %default_btn;
	@extend %btn_size_2;

	min-width: 23.4rem;

	&:hover,
	&:focus {
		.applicazioni_listato_hero__icon {
			color: $white;
		}
	}
}

.applicazioni_listato_hero__icon {
	@extend %transition_color;

	position: absolute;
	top: 50%;
	right: 10%;
	transform: translateY(-50%);

	color: $accent_color_4;

	width: 1rem;
	height: 1.2rem;
}

.applicazioni_listato_hero__img_block {
	@include bp($point_1, min) {
		margin-left: 2.5rem;
	}

	@include bp($point_2, min) {
		width: calc(100% + 2.9rem);
	}

	@include bp($point_3) {
		margin: 0 calc(var(--gl-indent) * -1);
	}
}

.applicazioni_listato_hero__img_w {
	display: block;
	width: 100%;
	height: 100%;

	transition: transform 2s ease;
}

