//---------------------------------------------main_menu
.widget_wrap {
	// display: none !important;
}

$link_decor_width: 7.2rem;
$nav_container_width: 33.33vw;

.main_menu {
	position: fixed;
	top: 0;
	left: 0;
	z-index: 100;

	width: 100%;
	height: 100%;

	font-size: 1rem;

	opacity: 0;
	pointer-events: none;
	background-color: $black;
	transition: opacity .3s ease;

	body.body--menu_active_state & {
		opacity: 1;
		pointer-events: auto;
	}
}

.main_menu__bg {
	@include bp($point_1, min) {
		position: absolute;
		top: 0;
		left: $nav_container_width;
		z-index: 1;

		width: $nav_container_width * 2;
		height: 100%;
	}

	@include bp($point_1 - 1) {
		display: none;
	}
}

.main_menu__bg_img {
	@include bp($point_1, min) {
		display: block;
		width: 100%;
		height: 100%;
	
		object-fit: cover;
	}
}

.main_menu__header {
	position: absolute;
	top: 0;
	left: 0;
	z-index: 10;

	height: var(--height-header);
	width: 100%;
	padding: 0 var(--gl-indent);

	pointer-events: none;

	@include flex_block(row, wrap, flex-start, center, center);
}

.main_menu__body {
	position: relative;

	padding-top: var(--height-header);
	// padding-bottom: 7rem;
	height: 100%;

	// background-color: $black;
}

.main_menu__container {
	height: 100%;

	@include bp($point_1, min) {
		width: $nav_container_width;
	}

	@include bp($point_1 - 1) {
		position: relative;
	}
}

.main_menu__container_in {
	height: 100%;

	overflow: hidden;
	overflow-y: auto;

	@include bp($point_1, min) {
		display: flex;
		flex-direction: row;
		margin-right: -($link_decor_width / 2);
		padding: 1.6rem ($link_decor_width / 2) 1.6rem 0;
	}

	@include bp($point_1 - 1) {
		// padding-top: 3rem;
		// padding-bottom: 3rem;
	}
}

.main_menu__list {
	width: 100%;

	@include bp($point_1, min) {
		margin: auto 0;
	}
}

.main_menu__item {
	@include bp($point_1, min) {
		&:not(:last-of-type) {
			// margin-bottom: 4.4rem;
			margin-bottom: 2.4rem;
		}
	}

	.main_menu__list:hover > & {
		> .main_menu__link {
			@include bp($point_1, min) {
				opacity: .5;
			}
		}
	}
}

.main_menu__heading {
	@include bp($point_1, min) {
		display: none;
	}

	@include bp($point_1) {
		display: flex;
		flex-direction: column;

		border-bottom: 1px solid $accent_color_3;
	}
}

.main_menu__link_icon {
	@include bp($point_1, min) {
		display: none;
	}

	@include bp($point_1 - 1) {
		width: 1rem;
		height: 2rem;
		margin-left: auto;

		.main_menu__link--back_mod & {
			margin-right: 1rem;
			margin-left: 0;
		}
	}
}

.main_menu__link {
	position: relative;

	width: 100%;
	padding-left: var(--gl-indent);
	padding-right: var(--gl-indent);

	color: $white;
	font-family: $roboto_font;
	font-weight: 300;
	text-align: left;

	transition: opacity .3s ease;
	text-decoration: none;

	&:hover,
	&:focus {
		opacity: 1;
		text-decoration: none;
	}

	.main_menu__item--dropdown_mod > & {
		&:before {
			@include bp($point_1, min) {
				position: absolute;
				top: 50%;
				right: -($link_decor_width / 2);
	
				width: $link_decor_width;
				height: 7.8rem;
				margin-top: -3.9rem;
	
				background: url('../images/icons/other_icons/arrow_right_with_bg.svg') no-repeat center center;
				background-size: contain;
				opacity: 0;
				pointer-events: none;
				transition: opacity .3s ease;
	
				content: '';
			}
		}
	}

	.main_menu__item--active_state > &,
	.main_menu__item:hover > & {
		@include bp($point_1, min) {
			opacity: 1 !important;

			&:before {
				opacity: 1;
			}
		}
	}

	.main_menu__dropdown & {
		@include bp($point_9 + 1, min) {
			padding-left: 14.6rem;
		}

		@media (min-width: $point_1) and (max-width: $point_9) {
			padding-left: calc(var(--gl-indent) + ($link_decor_width / 2));
		}
	}

	@include bp($point_9, min) {
		font-size: 3.6rem;
	}

	@media (min-width: $point_2) and (max-width: $point_9 - 1) {
		font-size: 3rem;
	}

	@include bp($point_1, min) {
		display: block;

		z-index: 5;
	}

	@include bp($point_1, min) {
		padding-top: 1rem;
		padding-bottom: 1rem;
	}

	@include bp($point_1 - 1) {
		display: flex;
		align-items: center;

		padding-top: 2rem;
		padding-bottom: 2rem;
	}

	@media (min-width: $point_4) and (max-width: $point_3) {
		font-size: 2.6rem;
	}

	@include bp($point_4 - 1) {
		font-size: 2.2rem;
	}
}

.main_menu__dropdown {
	top: 0;
	z-index: 1;

	height: 100%;

	opacity: 0;
	pointer-events: none;
	background-color: $accent_color_1;
	transition: opacity .3s ease;

	.main_menu__dropdown & {
		background-color: #979797;

		@include bp($point_1, min) {
			left: calc($nav_container_width * 2);
		}
	}

	// .main_menu__item--active_state > & {
	// 	opacity: 1;
	// 	pointer-events: auto;
	// }

	@include bp($point_1, min) {
		position: fixed;

		left: $nav_container_width;
		width: $nav_container_width;

		.main_menu__item:hover > & {
			opacity: 1;
			pointer-events: auto;
		}
	}

	@include bp($point_1 - 1) {
		position: absolute;

		left: 0;
		width: 100%;

		.main_menu__item--active_state > & {
			opacity: 1;
			pointer-events: auto;
		}
	}
}

