.product_list {
	overflow: hidden;
}

.product_list__list {
	@extend %global_flex_block_row_wrap_center;

	align-items: flex-end;
	align-content: stretch;

	margin: 0 -12rem -12.8rem 0;
}

.product_list__item {
	padding: 0 12rem 12.8rem 0;
	
	@include bp($point_2, min) {
		width: 33.33%;
	}

	@include bp($point_3, $point_4) {
		width: 50%;
	}

	@include bp($point_4 - 1) {
		width: 100%;
	}
}

.product_list__item_link {
	@extend %global_flex_block_column_nowrap_flex-start_center;

	&:hover,
	&:focus {
		text-decoration: none;
	}
}

.product_list__item_clip {
	margin-bottom: 3.7rem;

	max-width: 25rem;
	max-height: 25rem;
}

.product_list__item_picture {
	position: absolute;
	top: 0;
	left: 0;
	
	display: block;
	width: 100%;
	height: 100%;

	transition: transform .3s ease;

	.product_list__item:hover & {
		transform: scale(1.1);
	}
}

.product_list__item_img {
	display: block;
	width: 100%;
	height: 100%;

	object-fit: cover;
}

.product_list__item_title {
	font-weight: 300;
	font-size: 4.8rem;
	line-height: 6.5rem;
	text-align: center;
	color: $accent_color_2;

	transition: color .3s ease;

	.product_list__item:hover & {
		@media (hover: hover) {
			color: $accent_color_1;
		}
	}

	.product_list__item:focus & {
		@media (pointer: none) {
			color: $accent_color_1;
		}
	}
}

.product_list__item_overlay {
	position: absolute;
	top: 0;
	left: 0;

	width: 100%;
	height: 100%;

	background-color: $accent_color_1;
	mix-blend-mode: multiply;
	opacity: 0;

	transition: opacity .3s ease;

	.product_list__item:hover & {
		@media (hover: hover) {
			opacity: 1;
		}
	}

	.product_list__item:focus & {
		@media (pointer: none) {
			opacity: 1;
		}
	}
}

.product_list__item_icon {
	position: absolute;
	top: 55%;
	left: 59%;
	
	display: block;
	margin: -1.4rem -1.4rem;
	width: 2.8rem;
	height: 2.8rem;

	color: $white;
	opacity: 0;

	transition: opacity .3s ease;

	&.product_list__item_icon--position_1_mod {
		top: 53%;
		left: 52.5%;
	}

	&.product_list__item_icon--position_2_mod {
		top: 45%;
		left: 58.5%;
	}

	.product_list__item:hover & {
		@media (hover: hover) {
			opacity: 1;
		}
	}

	.product_list__item:focus & {
		@media (pointer: none) {
			opacity: 1;
		}
	}
}