//---------------------------------------------fonts
$font_path: "../fonts/";
$roboto_font: "Roboto", sans-serif;
$font_size_base: 1.6rem;
//---------------------------------------------fonts###

//---------------------------------------------colors
$white: #fff;
$black: #000;

$accent_color_1: #ff7700;
$accent_color_2: #575756;
$accent_color_3: #f6f6f6;
$accent_color_4: #6a665e;
$accent_color_5: #878786;
$accent_color_6: #b2b2b1;

$color_text_base: $accent_color_2;
$color_link_base: #4aa3df;
$body_bg: $white;
//---------------------------------------------colors###

//---------------------------------------------z-index
$z-index: (
	popup: 101,
	header: 100,
	footer: 100,
);
//---------------------------------------------z-index###

//---------------------------------------------layout
$page_width: 96rem;
$page_width_2: 156rem;
$page_width_3: 124rem;

//---------------------------------------------layout###

//---------------------------------------------global values
$gl_radius: 0.5rem;
//---------------------------------------------global values###

//---------------------------------------------media points
$point_1: 1200px;
$point_2: 1024px;
$point_3: 1023px;
$point_4: 768px;
$point_5: 640px;
$point_6: 480px;
$point_7: 375px;
$point_8: 320px;
$point_9: 1480px;
$point_10: 1280px;
$point_11: 1670px;
//---------------------------------------------media points

//---------------------------------------------forms
$input_height: 4rem;
$input_offset: 0;
$input_font_size: 1.6rem;
$input_border_color: $accent_color_2;
$input_border_color_active: $accent_color_1;
$input_placeholder_color: rgba($black, 0.4);
$input_text_color: $accent_color_1;
$input_radius: $gl_radius;
//---------------------------------------------forms###
