.key_hero {
	padding-top: 17.2rem;
	padding-bottom: 3.2rem;
}

.key_hero__block {
	@extend %global_flex_block_row_wrap_flex-start;

	max-width: $page_width_2;
	margin: 0 auto;
}

.key_hero__col {
	&:first-child {
		@include bp($point_2, min) {
			width: 55%;
			padding: 11.6rem 6rem 0 0;
		}

		@include bp($point_3) {
			order: 2;
			width: 100%;
		}
	}

	&:last-child {
		@include bp($point_2, min) {
			width: 45%;
		}

		@include bp($point_3) {
			width: 100%;
			margin-bottom: 3rem;
		}
	}
}

.key_hero__title {
	@extend %h0;

	@include bp($point_2, min) {
		margin-bottom: 4.5rem;
	}

	@include bp($point_3) {
		margin-bottom: 2rem;
		text-align: center;
	}
}

.key_hero__text {
	@extend %descrip;

	@include bp($point_2, min) {
		margin-bottom: 6.1rem;
	}

	@include bp($point_3) {
		margin-bottom: 3rem;
		text-align: center;
	}
}

.key_hero__btn_wrap {
	@include bp($point_3) {
		@include flex_block(row, wrap, center);
	}
}

.key_hero__btn {
	@extend %default_btn;
	@extend %btn_size_2;

	min-width: 23.4rem;
}

.key_hero__btn_icon {
	position: absolute;
	top: 50%;
	right: 10%;
	transform: translateY(-50%);

	width: 1rem;
	height: 1.2rem;
}

.key_hero__img {
	display: block;
	width: 100%;
}

.key_hero__img_block {
	@include bp($point_2, min) {
		width: calc(100% + 6rem);
		margin-left: -0.6rem;
	}

	@include bp($point_3) {
		margin: 0 calc(var(--gl-indent) * -1);
	}
}

.key_hero__img_in {
	x: -3.1%;
	y: -18.8%;
}
