.about_us_hero {
	@include bp($point_2, min) {
		padding: 11.6rem var(--gl-indent) 26.2rem;
	}

	@include bp($point_3) {
		padding: 10rem var(--gl-indent) 12rem;
	}
}

.section__in {
  .about_us_hero & {
    max-width: $page_width_2;
  }
}

.about_us_hero__row {
  @extend %global_flex_block_row_wrap_flex-start;
}

.about_us_hero__column {

  @include bp($point_2, min) {
    &:first-child {
      flex: 0 0 39%;
    }
    &:last-child {
      flex: 0 0 61%;
    }
  }

  @include bp($point_1, min) {
    &:last-child {
      padding: 20rem 0 2rem 19.2rem;
    }
  }

  @include bp($point_1 - 1, $point_2) {
    &:last-child {
      padding: 12rem 0 2rem 10.2rem;
    }
  }

  @include bp($point_3) {
    flex: 0 0 100%;

    text-align: center;
    &:first-child {
      &:not(:last-child) {
        margin-bottom: 3rem;
      }
    }

  }
}


.about_us_hero__img_wrap {

  @include bp($point_2, min) {
    width: calc(100% + 36.4rem);
    margin-left: -36.6rem;
  }

  @include bp($point_3) {
    width: 100%;
  }

}

.about_us_hero__title {
  @extend %h1;

	color: $accent_color_2;

	@include bp($point_2, min) {
		line-height: 1;

		&:not(:last-child) {
			margin-bottom: 4.8rem;
		}
	}
	@include bp($point_3) {
		font-size: 2.4rem;
		line-height: 1.3;

		&:not(:last-child) {
			margin-bottom: 1.8rem;
		}
	}

	strong {
		display: block;
		font-weight: 300;
		color: $accent_color_1;

		@include bp($point_2, min) {
			font-size: 6.8rem;
		}
		@include bp($point_3) {
			font-size: 3.4rem;
		}
	}
}

.about_us_hero__img_mask {
  width: 100%;
}

.about_us_hero__img {
	display: block;
  width: 100%;
  height: 100%;
}

.about_us_hero__img_in {
  @extend %contain_img;
}

.about_us_hero__descr {
  @extend %descrip;

  strong {
    font-weight: 500;
  }

  @include bp($point_2, min) {
    &:not(:last-child) {
      margin-bottom: 5.2rem;
    }
  }

  @include bp($point_3) {
    &:not(:last-child) {
      margin-bottom: 3.2rem;
    }
  }
}
.about_us_hero__descr_block {
  &:not(:last-child) {
    margin-bottom: 3rem;
  }
}


.about_us_hero__button {
  @extend %default_btn;
	@extend %btn_size_1;
}
