%form_el_checkbox {
	position: relative;

	display: block;
	padding-left: 3rem;

	font-size: 1.2rem;
	color: $accent_color_2;
	cursor: pointer;

	&:before,&:after {
		position: absolute;
		top: -1px;
		left: 0;
		width: 1.6rem;
		height: 1.6rem;
		cursor:pointer;
	}

	&:before {
		content: '';
		border-radius: 2px;
		border: 1px solid $black;
	}

	&:after {
		@extend %transition_opacity;

		content: '';

		text-align: center;
		line-height: 2rem;
		opacity: 0;
		color: $black;

		background: url('../images/checked.svg') no-repeat center center;
		background-size: 50% 50%;

		.form_checkbox__element:checked + & {
			opacity: 1;
		}
	}
}