.contacts_hero {
	@extend %global_flex_block_row_wrap_flex-start_center;

	min-height: 100vh;

	@include bp($point_2, min) {
		padding-top: 15.6rem;
		padding-bottom: 5rem;
	}
	@include bp($point_3) {
		padding-top: 10rem;
		padding-bottom: 4rem;
	}

	&::after {
		z-index: -2;
		position: absolute;
		left: 0;
		top: 0;

		width: 100%;

		background-color: $accent_color_3;

		content: '';

		@include bp($point_2, min) {
			height: 78.2%;
		}
		@include bp($point_3) {
			height: 100%;
		}
	}
}

.contacts_hero__in {
	max-width: $page_width_2;

	@include bp($point_2, min) {
		@include flex_block(row, nowrap, flex-start, center, center);
	}
	@include bp($point_3) {
		display: block;
	}
}

.contacts_hero__content {
	flex: auto;

	@include bp($point_2, min) {
		padding: 0 4rem 12rem 0;
	}
	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}
}

.contacts_hero__title {
	@extend %h1;


	@include bp($point_2, min) {
		font-size: 6.8rem;

		&:not(:last-child) {
			margin-bottom: 5rem;
		}
	}
	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 3rem;
		}
	}
}

.contacts_hero__descr {
	font-weight: 300;
	line-height: 1.5;
	color: $accent_color_2;

	@include bp($point_2, min) {
		font-size: 2rem;

		&:not(:last-child) {
			margin-bottom: 8.4rem;
		}
	}
	@include bp($point_3) {
		@include flex_block(row, wrap, flex-start, center, center);

		margin: 0 -8rem -1.5rem 0;
		font-size: 1.6rem;

		&:not(:last-child) {
			margin-bottom: 3rem;
		}
	}

	p {
		&:not(:last-child) {
			@include bp($point_2, min) {
				margin-bottom: 1.5em;
			}
		}
		@include bp($point_3) {
			padding: 0 8rem 1.5rem 0;
		}
	}

	strong {
		font-weight: 700;
	}

	a {
		color: $accent_color_2;
		transition: color .3s;

		&:hover,
		&:focus {
			text-decoration: none;
			color: rgba($accent_color_2, .6);
		}
	}
}

.contacts_hero__link {
	@extend %white_btn;
	@extend %btn_size_1;
	padding: 0 4rem;

	&::after {
		z-index: 1;
	}
}

.contacts_hero__link_title {
	z-index: 2;
	position: relative;
}

.contacts_hero__link_icon {
	z-index: 2;
	position: absolute;
	right: 2rem;
	top: 50%;

	margin-top: -.7rem;
	width: 1.2rem;
	height: 1.2rem;
}

.contacts_hero__map {

}

.contacts_hero__map_wrap {

	@include bp($point_2, min) {
		width: 46.4vw;
		height: 46.4vw;
	}
	@include bp($point_3) {
		width: calc(100vw - var(--gl-hr-indent) * 2);
		height: calc(100vw - var(--gl-hr-indent) * 2);
	}
}

.contacts_hero__map_frame {
	height: 88.6rem;
	width: 100%;
}