.applicazioni_prodotto_case {
	background-color: $accent_color_3;

	@include bp($point_9 + 1, min) {
		margin-top: -25rem;
		padding: 28rem var(--gl-indent) 22.6rem;
	}

	@include bp($point_9, $point_2) {
		padding: 18rem var(--gl-indent) 12.7rem;
	}

	@include bp($point_3) {
		padding: 9rem var(--gl-indent) 6rem;
	}
}

.applicazioni_prodotto_case__in {
	max-width: $page_width_2;
}

.case_slider__wrapper {
	position: relative;

	@include bp($point_2, min) {
		padding-bottom: 15.1rem;
	}

	@include bp($point_3) {
		padding-bottom: 10rem;
	}
}

.case_slider__arrow {
	position: absolute;
	bottom: 0;
	left: 50%;
	z-index: 2;
	cursor: pointer;
	transition: color 0.3s;

	.case_slider__icon {
		@extend %transition_transform;

		height: 1.8rem;
		width: 100%;
	}

	span {
		display: block;
		text-transform: uppercase;
		font-weight: 700;
	}

	@include bp($point_6 + 1, min) {
		width: 21.5rem;

		span {
			font-size: 1.4rem;
			letter-spacing: 0.2rem;
		}
	}

	@include bp($point_6) {
		width: 12rem;

		span {
			font-size: 1.2rem;
		}
	}
}

.case_slider__arrow--prev {
	transform: translateX(calc(-100% - 2rem));

	span {
		text-align: right;
	}

	&:hover {
		color: $accent_color_1;

		.case_slider__icon {
			height: 1.8rem;
			transform: scaleX(1.1) translateX(-4.6%);
		}
	}
}

.case_slider__arrow--next {
	transform: translateX(2rem);

	&:hover {
		color: $accent_color_1;

		.case_slider__icon {
			transform: scaleX(1.1) translateX(4.6%);
		}
	}
}

.case_title {
	@extend %h1;

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 6.7rem;
		}
	}

	@include bp($point_3) {
		text-align: center;

		&:not(:last-child) {
			margin-bottom: 3.4rem;
		}
	}
}

.case_item {
	@include bp($point_2, min) {
		margin-right: -9vw;

		@include flex_block(row, nowrap, flex-start);
	}

	@include bp($point_3) {
		@include flex_block(column, nowrap, flex-start, center, center);
	}
}

.case_item__col {
	@include bp($point_2, min) {
		padding-right: 9vw;

		&:first-child {
			width: 46.2%;
		}

		&:nth-child(2) {
			width: 53.8%;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 3rem;
		}
	}
}

.case_item__image {
	@include bp($point_3) {
		max-width: 62.7rem;
		width: 100%;
		margin: 0 auto;
	}
}

.case_item__mask {
	width: 100%;
	height: 100%;
}

.case_item__logo {
	display: block;

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 2.3rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 1.8rem;
		}
	}
}

.case_item__content {
	@extend %descrip;

	@include bp($point_1, min) {
		padding-top: 6.3rem;
	}

	@include bp($point_2, min) {
		p:not(:last-child) {
			margin-bottom: 2.9rem;
		}
	}

	@include bp($point_3) {
		text-align: center;

		p:not(:last-child) {
			margin-bottom: 2rem;
		}
	}
}
