.collaborate--mod_1 {
	padding-bottom: 3.2rem;

	@include bp($point_2, min) {
		padding-top: 29.1rem;

		.collaborate_text:not(:last-child) {
			margin-bottom: 5.9rem;
		}
	}

	@include bp($point_3) {
		padding-top: 13rem;

		.collaborate_text:not(:last-child) {
			margin-bottom: 4rem;
		}
	}
}

.collaborate--mod_2 {
	padding-top: 3rem;
	padding-bottom: 5.1rem;
}

.collaborate--mod_3 {
	padding-top: 5rem;
	padding-bottom: 5.1rem;
}

.collaborate--mod_4 {
	padding-top: 5rem;

	@include bp($point_2, min) {
		padding-bottom: 20.9rem;

		.collaborate_text:not(:last-child) {
			margin-bottom: 10rem;
		}
	}

	@include bp($point_3) {
		padding-bottom: 14rem;

		.collaborate_text:not(:last-child) {
			margin-bottom: 7rem;
		}
	}
}

.collaborate__decor {
	position: absolute;
	z-index: -1;
	top: 50%;
	left: 50%;

	fill: $accent_color_3;

	&.collaborate__decor--mod_1 {
		margin: -25.3% 0 0 13.3%;

		width: 23.8vw;
		height: 26.2vw;
	}

	&.collaborate__decor--mod_2 {
		margin: -4.5% 0 0 29.6%;

		width: 16.6vw;
		height: 18.3vw;
	}

	&.collaborate__decor--mod_3 {
		margin: -0.5% 0 0 1.2%;

		width: 9.45vw;
		height: 10.4vw;
	}
}

.collaborate__in {
	max-width: $page_width_2;
}

.collaborate__body--mod_1 {
	max-width: 76rem;
}

.collaborate__body--mod_2 {
	max-width: 108rem;
}

.collaborate__main_title {
	@extend %h0;

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 4.3rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 2.8rem;
		}
	}
}

.collaborate__title {
	@extend %h1;

	@include bp($point_2, min) {
		&:not(:last-child) {
			margin-bottom: 2rem;
		}
	}

	@include bp($point_3) {
		&:not(:last-child) {
			margin-bottom: 1.8rem;
		}
	}
}

.collaborate_text {
	@extend %descrip;

	strong {
		font-weight: 700;
		color: $accent_color_1;
	}

	@include bp($point_2, min) {
		p:not(:last-child) {
			margin-bottom: 3rem;
		}
	}

	@include bp($point_3) {
		p:not(:last-child) {
			margin-bottom: 2.6rem;
		}
	}
}

.collaborate__button {
	@extend %btn_size_1;
	@extend %default_btn;
}
