.technology {
	@include bp($point_2, min) {
		padding-top: 10vw;
		padding-bottom: 15.8rem;
	}

	@include bp($point_3) {
		padding-top: 15.8rem;
		padding-bottom: 5.2rem;
	}

	@include bp($point_5 - 1) {
		background-color: $accent_color_3;
	}
}

.technology__decor {
	position: absolute;
	top: 0;
	left: 50%;

	color: $accent_color_3;

	svg {
		display: block;
		width: 100%;

		fill: currentColor;
	}

	&.technology__decor--v1_mod {
		width: 100vw;
		margin-left: -50vw;

		z-index: -1;

		@include bp($point_3) {
			display: none;
		}
	}

	&.technology__decor--v2_mod {
		@include bp($point_2, min) {
			width: 56.66vw;
			height: 62.7vw;
			margin: 3.2vw 0 0 -40.6vw;
		}

		@include bp($point_3, $point_5) {
			width: 136vw;
			height: 150.4vw;
			margin: 28vw 0 0 -68vw;
		}

		@include bp($point_5 - 1) {
			display: none;
		}
	}

	&.technology__decor--v3_mod {
		width: 22.7vw;
		height: 25.1vw;

		margin: 13.4vw 0 0 17.4vw;

		@include bp($point_3) {
			display: none;
		}
	}

	&.technology__decor--v4_mod {
		width: 9.1vw;
		height: 10vw;
		margin: 27.8vw 0 0 40.8vw;

		@include bp($point_3) {
			display: none;
		}
	}

	&.technology__decor--v5_mod {
		width: 226vw;
		height: 250vw;
		margin: 24vw 0 0 -113vw;

		@include bp($point_5, min) {
			display: none;
		}
	}
}

.technology__block {
	position: relative;

	z-index: 2;

	@include bp($point_2, min) {
		width: 57vw;
		padding: 0 2rem;
		margin: 0 0 15.1rem calc(9vw - var(--gl-indent));
	}

	@include bp($point_3) {
		margin-bottom: 5.6rem;
	}
}

.technology__block_in {
	max-width: 72rem;
	margin: 0 auto;
}

.technology__logo {
	display: block;

	@include bp($point_1) {
		margin-bottom: 6rem;
	}

	@include bp($point_2, min) {
		width: 11.2rem;
		height: 10.2rem;
		margin: 0 auto 17.8rem;
	}

	@include bp($point_3) {
		width: 7.7rem;
		height: 7rem;
		margin: 0 auto 6.8rem;
	}
}

.technology__logo_in {
	@extend %contain_img;

	@include bp($point_2, min) {
		margin-left: 0.4rem;
	}
}

.technology__title {
	@extend %h1;

	text-align: center;

	@include bp($point_2, min) {
		margin-bottom: 1.4rem;
	}

	@include bp($point_3) {
		max-width: var(--width-content);
		padding: 0 1.5rem;
		margin: 0 auto 0.8rem;
	}
}

.technology__text {
	@extend %descrip;

	text-align: center;

	@include bp($point_2, min) {
		margin-bottom: 6.2rem;
	}

	@include bp($point_3) {
		max-width: var(--width-content);
		margin: 0 auto 3.9rem;
		//margin: 0 -1.2rem 3.9rem;
	}
}

.technology__btn_wrap {
	@extend %global_flex_block_row_nowrap_center;
}

.technology__btn {
	@extend %btn_size_1;
	@extend %default_btn;
}

.technology__img {
	position: relative;

	margin: 0 calc(var(--gl-indent) * -1);
	width: calc(100% + var(--gl-indent) * 2);

	z-index: 2;
}

.technology__img_in {
	display: block;

	@include bp($point_2, min) {
		width: calc(100vw - 2rem);
		margin-left: calc(var(--gl-indent) * -1);
	}

	@include bp($point_3) {
	width: 100%;

	}
}
