.app_list_sliders {
	padding: 0 var(--gl-indent) 13rem;

	@include bp($point_9, min) {
		margin-top: -27.6rem;
	}
}

.app_list_sliders__block_w {
	position: relative;

	margin: 0 calc(var(--gl-indent) * -1);

	&.app_list_sliders__block_w--mod_1 {
		min-height: 101rem;

		background-color: $accent_color_3;

		@include bp($point_9, min) {
			padding: 40.2rem 2rem 12rem 22rem;
		}

		@media screen and (max-width: $point_9 - 1) and (min-width: $point_10) {
			padding: 40.2rem 2rem 12rem 3rem;
		}

		@media screen and (max-width: $point_10 - 1) and (min-width: $point_2) {
			min-height: auto;
			padding: 17.2rem 2rem 12rem 3rem;
		} 

		@include bp($point_3) {
			min-height: auto;
			padding: 11.2rem 2rem 12rem 3rem;
		}
	}

	&.app_list_sliders__block_w--mod_2 {
		min-height: 57rem;

		@include bp($point_9, min) {
			padding: 16.6rem 2rem 3rem 22rem;
		}

		@media screen and (max-width: $point_9 - 1) and (min-width: $point_10) {
			padding: 18rem 2rem 3rem 3rem;
		}

		@media screen and (max-width: $point_10 - 1) and (min-width: $point_2) {
			padding: 11rem 2rem 3rem 3rem;
		}

		@include bp($point_3) {
			min-height: auto;
			padding: 5.2rem 2rem 3rem 3rem;
		}
	}
}

.app_list_sliders__block {
	@extend %global_flex_block_row_wrap_flex-start;

	@include bp($point_10 - 1) {
		flex-direction: column;
	} 
}

.app_list_sliders__block_title {
	@include bp($point_10, min) {
		width: 21%;
		padding-top: 5.4rem;
	}

	@include bp($point_10 - 1) {
		width: 100%;
		margin-bottom: 8rem;
	}
}

.app_list_sliders__block_title_text {
	font-size: 4.8rem;
	font-weight: 300;
	text-align: center;
	color: $accent_color_1;

	@media screen and (max-width: $point_10 - 1) and (min-width: $point_5) {
		text-align: left;
	}

	@include bp($point_5 - 1) {
		text-align: center;
	}
}

.app_list_sliders__block_slider_w {
	@include bp($point_10, min) {
		width: 74%;

		margin-left: auto;
	}

	@include bp($point_10 - 1) {
		width: 100%;
	}
}

.app_list_sliders__block_slider {
	@extend %global_flex_block_row_wrap_flex-start;

	margin-bottom: 5rem;
}

.app_list_sliders__block_slider__item {
	
}

.product_item--app_list_sliders-mod {
	position: relative;

	font-size: 2rem;
	font-weight: 300;

	& .product_item__img {
		@extend %transition_opacity;

		height: 20rem;

		margin-bottom: 5.4rem;

		img {
			@extend %transition_transform;
		}

		@include bp($point_2, min) {
			width: 20rem;
		}

		@include bp($point_3) {
			width: 29rem;
		}
	}

	& .product_item__descr {
		position: static;
		text-align: center;
	}
}

.product_item--app_list_sliders-mod:hover {
	text-decoration: none;
	
	& .product_item__img {
		//transform: scale(1.2);
		opacity: .5;

		img {
			transform: scale(1.1);
		}
	}

	& .product_item__descr {
		color: $accent_color_1;
	}
}

.app_list_sliders__block_slider__nav {
	@extend %global_flex_block_row_wrap_flex-start_center;
}

.app_list_sliders__block_slider__arrow {
	@extend %transition_opacity;

	width: 3.6rem;
	height: 1.8rem;

	color:#6a665e;

	&.swiper-button-disabled {
		opacity: 0;
	}
}

.app_list_sliders__block_slider__scrollbar {
	position: relative;

	width: calc(100% - (3.6rem + 6px) * 2);
  height: 2rem;
  
	margin: 0 3px;

  cursor: pointer;

  z-index: 3;

	&::after {
		z-index: -1;
    position: absolute;
    top: 50%;
    left: 0;

    width: 100%;
    height: 1px;
		
    background-color: #d8d8d8;
    content: "";
	}

	& .swiper-scrollbar-drag {
		background: url(../images/decor_scrollbar.svg) no-repeat center center;
		cursor: grab;
		
		.app_list_sliders--mod_2 & {
			background: url(../images/scrollbar_decor.svg) no-repeat center center;
		}
	}
}