//------------------------------------------------------------layout
.header {
	position: fixed;
	top: 0;
	left: 0;

	height: var(--height-header);
	width: 100%;
	padding: 0 var(--gl-indent);

	font-size: 1rem;

	transition: background-color 0.3s ease, transform 0.3s ease;
	will-change: background-color, transform;

	@include z-index(header);
	@include flex_block(row, wrap, flex-start, center, center);

	&.header--scroll_mod {
		box-shadow: 0 1px 4px rgba($black, 0.2);
		background-color: $white;
	}

	&.header--pos_mod {
		transform: translateY(-100%);
	}
}

.header__menu {
	@include bp($point_9, min) {
		// margin-right: 4.2rem;
		margin-right: 4vw;
	}

	@include bp($point_9 - 1) {
		display: none;
	}
}

.header__container {
	margin-left: auto;

	@include bp($point_9, min) {
		@include flex_block(row, wrap, flex-start, center, center);
	}
}

.header__menu {
	@include bp($point_9, min) {
		@include flex_block(row, wrap, flex-start, center, center);
	}
}

.header__menu_list {
	@include bp($point_9, min) {
		@include flex_block(row, wrap, flex-start, center, center);
	}
}

.header__menu_item {
	@include bp($point_9, min) {
		position: relative;

		&:not(:last-of-type) {
			margin-right: 4rem;
		}

		&.header__menu_item--dropdown_mod {
		}
	}
}

.header__menu_link {
	@include bp($point_9, min) {
		display: flex;
		align-items: center;

		color: $black;
		font-size: 1.6rem;
		font-weight: 300;
		text-transform: uppercase;
		text-decoration: none;

		border-bottom: 1px solid transparent;

		transition: color 0.3s ease;

		&:hover,
		&:focus,
		.header__menu_item:hover {
			text-decoration: none;
			color: $accent_color_1;
		}
	}
}

.header__menu_link_icon {
	@include bp($point_9, min) {
		margin-left: 0.8rem;
		width: 0.8rem;
		height: 0.4rem;

		transition: transform 0.3s ease;

		.header__menu_item--dropdown_mod:hover &,
		.header__menu_item--dropdown_mod:focus & {
			transform: rotate(180deg);
		}
	}
}

.header__menu_item_hexagon {
	position: absolute;
	top: -7rem;
	left: 50%;

	display: block;
	width: 3.7rem;
	height: 3.7rem;

	color: $accent_color_1;

	transform: translateX(-50%);

	transition: transform 0.3s ease;

	.header__menu_item:hover & {
		transform: translate(-50%, 2rem);
	}
}
//------------------------------------------------------------layout###
