.team {
	background-color: $accent_color_3;
}

.team__decor{
	position: absolute;
	top: -7.7vw;
	left: 0;
	transform: translateY(-100%);
	z-index: -2;

	width: 100%;
	height: 5.4vw;

	&::after{
		content:'';
		position: absolute;
		top: -6.7vw;
		left: 0;
		transform: translateY(100%);

		width: 100%;
		height: 12vw;

		background-color: $accent_color_3;
	}
}

.team__block {
	max-width: 161rem;
	margin: 0 auto;
}

// links

.team__head {

	@include bp($point_2, min) {
		margin-bottom: 12.6rem;
	}

	@include bp($point_3, $point_5 - 1) {
		margin-bottom: 8.6rem;
	}

	@include bp($point_5 - 1) {
		margin-bottom: 6.6rem;
	}
}

.team__head_list {
	@extend %global_flex_block_row_wrap_center;

	margin: 0 -10rem -2rem 0;
}

.team__head_item {
	padding: 0 10rem 2rem 0;

	@include bp($point_5 - 1) {
		width: 100%;
		text-align: center;
	}
}

.team__head_link {
	padding-bottom: 1rem;

	font-size: 1.6rem;
	font-weight: 700;
	color: $accent_color_2;
	letter-spacing: .2rem;
	text-transform: uppercase;

	border-bottom: 2px solid transparent;
	opacity: .5;

	transition: color .3s, border-color .3s;

	&.team__head_link--active_state {
		color: $accent_color_1;
		opacity: 1;
		border-color: $accent_color_1;
	}

	&:hover, &:focus {
		text-decoration: none;
	}
}


// content

.team__tab_content {
	display: none;

	&.team__tab_content--active_state {
		display: block;
	}
}

.team__list {
	@extend %global_flex_block_row_wrap_center;


	@include bp($point_2, min) {
		margin: 0 -4rem -10.4rem 0;
	}

	@include bp($point_3, $point_5) {
		margin: 0 -4rem -6.4rem 0;
	}


	@include bp($point_5 - 1) {
		margin: 0 0 -4rem 0;
	}
}

.team__item {

	@include bp($point_2, min) {
		width: 33.33%;
		padding: 0 4rem 10.4rem 0;
	}

	@include bp($point_3, $point_5) {
		width: 50%;
		padding: 0 4rem 6.4rem 0;
	}

	@include bp($point_5 - 1) {
		width: 100%;
		padding: 0 0 4.8rem 0;
	}


}

.team__item_in {
	max-width: 44rem;
	margin: 0 auto;

	&:hover, &:focus {
		text-decoration: none;
	}
}

.team__item_wrap {
	position: relative;
	width: 28.2rem;
	max-width: 100%;
	margin: 0 auto 2.2rem;

	&:before {
		content: '';
		display: block;
		padding-bottom: 128.4%;
	}
}

.team__img {
	position: absolute;
	top: 0;
	left: 0;
}

.team__img_in {
	display: block;
	width: 100%;
	// height: 100%;
	// object-fit: cover;
}

.team__img_decor {
	opacity: 0;

	transition: opacity .3s;

	&.team__img_decor--bg_mod {
		mix-blend-mode: multiply;
	}

	.team__item_in:hover &,
	.team__item_in:focus & {
		opacity: 1;
	}
}

.team__name {
	@extend %h1;

	margin-bottom: .6rem;

	color: $accent_color_2;
	text-align: center;

	transition: color .3s;

	.team__item_in:hover &,
	.team__item_in:focus & {
		color: $accent_color_1;
	}
}

.team__job {
	font-size: 1.4rem;
	font-weight: 700;
	color: $accent_color_4;
	opacity: .5;
	text-align: center;
	text-transform: uppercase;

	transition: color .3s;

	.team__item_in:hover &,
	.team__item_in:focus & {
		color: $accent_color_1;
	}
}
