.applications {
	background-color: $accent_color_3;

	@include bp($point_2, min) {
		padding: 10.3rem 4rem 12.4rem;
	}

	@include bp($point_3) {
		padding: 2.2rem 2rem 0;
	}

	&	.section__in {
		max-width: $page_width_2;
	}
}

.applications__heading {
	@include bp($point_2, min) {
		max-width: 72rem;
		margin-bottom: 15rem;
	}

	@include bp($point_3) {
		margin-bottom: 3.6rem;
	}
}

.applications__title {
	@include bp($point_2, min){
		@include flex-block(row, wrap, flex-start, flex-start, flex-start);
		margin-bottom: 1.2rem;
	}

	@include bp($point_3){
		@include flex-block(column, wrap, flex-start, center, center);
		margin-bottom: 1.4rem;
	}
}

.applications__title_wrap{
	display: block;

	@include bp($point_2, min){
		width: 6.2rem;
		height: 5.4rem;

		margin-right: 2rem;
	}

	@include bp($point_3){
		width: 6rem;
		height: 5.2rem;

		margin-bottom: 2.3rem;
	}
}

.applications__title_img {
	@extend %contain_img;
}

.applications__title_text {
	@extend %h1;
}

.applications__descr {
	@extend %descrip;

	@include bp($point_3){
		text-align: center;
		margin: 0 -0.01rem;
	}
}

//---------------------------------TABS STYLES

.applications__tabs {
	@include bp($point_1, min) {
		@include flex-block(row, wrap, flex-start, flex-start, flex-start);
	}

	@include bp($point_1 - 1) {
		display: none;
	}
}

.applications__tabs_nav {
	width: 100%;

	@include bp($point_9, min) {
		max-width: 39.4%;
	}

	@include bp($point_9 - 1) {
		max-width: 24%;
	}
}

.applications__tabs_nav_list {
	min-width: 31.8rem;
	padding: .4rem 0 .4rem 5.4rem;
}

.applications__tabs_nav_item {
	@extend %global_flex_block_row_wrap_flex-start_center;
	@extend %transition_color;

	position: relative;

	height: 10rem;

	font-size: 3.6rem;
	font-weight: 300;
	color: $accent_color_2;

	&:hover,
	&:focus {
		text-decoration: none;
		color: $accent_color_1;
	}

	&.applications__tabs_nav_item--active_state {
		color: $accent_color_1;
	}

	&::before {
		position: absolute;
		top: 0;
		left: -5.4rem;

		// width: .3rem;
		width: .1rem;
		height: 100%;

		content: '';

		// background-color: rgba($accent_color_2, .3);
		background-color: $accent_color_2;
	}

	// &.applications__tabs_nav_item--active_state::before {
	// 	background-color: $accent_color_1;
	// }

	&::after{
		@extend %transition_opacity;
		content:'';
		position: absolute;
		top: 0;
		left: -5.4rem;

		width: .3rem;
		height: 100%;

		opacity: 0;
		background-color: $accent_color_1;
	}

	&.applications__tabs_nav_item--active_state::after {
		opacity: 1;
	}
}

.applications__tabs_nav_img {
	@extend %transition_opacity;

	width: 1.8rem;
	height: .9rem;

	margin-left: 4.4rem;

	color: $accent_color_1;	

	opacity: 0;

	@media screen and (max-width: 1280px) {
		display: none;		
	}

	.applications__tabs_nav_item--active_state & {
		opacity: 1;
	}
}

.icon_arrow_right {
	width: 100%;
	height: 100%;
}

//-----------------------------TABS CONTENT

.applications__tabs_content_wrap {
	position: relative;

	width: 100%;

	@include bp($point_9, min) {
		max-width: 60%;
	}

	@include bp($point_9 - 1) {
		max-width: 76%;
	}
}

.applications__tabs_content {
	display: none;
	padding-top: 2rem;

	&.applications__tabs_content--active_state {
		display: block;
	}
}

.applications__tabs_content__list {
	@extend %global_flex_block_row_wrap_center;

	width: 100%;

	margin-top: -4rem;

	&:nth-child(1) {
		margin: 0;
	}
}

.applications__tabs_content__list_item {
	position: relative;

	width: 20rem;
	height: 22rem;

	margin-right: 1rem;

	text-align: center;
}

.product_item {
	@extend %global_flex_block_column_wrap_center_center;

	width: 100%;
	height: 100%;
}

.product_item__img {
	position: relative;
	z-index: 1;

	display: block;

	img {
		@extend %contain_img;
	}

	@include bp($point_1 - 1) {
		min-height: 17rem;
	}
}

.product_item__descr {
	position: absolute;
	top: 106%;

	font-weight: 300;
	color: $accent_color_2;
}

.product_item__bg {
	position: absolute;
	z-index: 0;
	top: 0;
	left: 0;

	width: 100%;
	height: 100%;

	color: $white;

	& svg {
		display: block;
    width: 100%;
    height: 100%;
    fill: currentColor;
	}

	.product_item__overlay & {
		color: $accent_color_1
	}

	.product_item--decor & {
		color: rgba($black, .1);
	}
}

.product_item__overlay {
	@extend %global_flex_block_row_wrap_center_center;
	@extend %transition_opacity;

	position: absolute;
	z-index: 1;
	top: 0;
	left: 0;

	width: 100%;
	height: 100%;

	opacity: 0;

	& span {
		position: relative;
		z-index: 2;

		font-size: 2rem;
		font-weight: 300;
		line-height: 3rem;
		color: $white;
	}

	.product_item:hover & {
		opacity: 1;
	}

	.product_item.product_item--decor:hover & {
		opacity: 0;
	}

	.applications_accordion__content_list_item & {
		display: none;
	}
}
//----------------------------------------#

@import "applications_accordion";