//---------------------------------------------menu_trigger
.menu_trigger {
	@include flex_block(row, wrap, center, center, center);

	position: relative;

	width: 5.2rem;
	height: 5.8rem;

	color: $accent_color_1;
	cursor: pointer;
	transition: color .3s ease;

	&:hover,
	&:focus {
		color: darken($accent_color_1, 10%);
	}

	.main_menu__header & {
		margin-left: auto;
		pointer-events: auto;
	}
}

.menu_trigger__bg {
	position: absolute;
	top: 0;
	left: 0;

	width: 100%;
	height: 100%;

	svg {
		display: block;
		width: 100%;
		height: 100%;

		object-fit: cover;
		fill: currentColor;
	}
}

.menu_trigger__in {
	position: relative;

	height: 1.4rem;
	width: 2rem;

	color: transparent;
	cursor: pointer;

