$menu_trigger_cancel_icon_size: 2.2rem;

.menu_trigger__decor {
	position: relative;
	z-index: 1;

	color: $white;

	transition: opacity .3s ease;

	svg {
		fill: currentColor;
	}

	&.menu_trigger__decor--hamburger {
		width: 2rem;
		height: 1.4rem;

		.menu_trigger--active_state & {
			opacity: 0;
		}
	}

	&.menu_trigger__decor--cancel {
		position: absolute;
		top: 50%;
		left: 50%;

		width: $menu_trigger_cancel_icon_size;
		height: $menu_trigger_cancel_icon_size;
		margin-left: -($menu_trigger_cancel_icon_size / 2);
		margin-top: -($menu_trigger_cancel_icon_size / 2);

		opacity: 0;

		.menu_trigger--active_state & {
			opacity: 1;
		}
	}
