.news {
	padding-top: 4rem;

	@include bp($point_2, min) {
		padding-bottom: 15rem;
	}

	@include bp($point_3) {
		padding-bottom: 11rem;
	}
}

.news__in {
	max-width: $page_width_3;
}

.news__title {
	@extend %h1;

	text-align: center;
	
	@include bp($point_2, min) {
		margin-bottom: 4.7rem;
	}

	@include bp($point_3) {
		margin-bottom: 3.4rem;
	}
}

.news__block {
	@include bp($point_2, min) {
		margin-bottom: 12rem;
	}

	@include bp($point_3) {
		margin-bottom: 3.9rem;
	}
}

.news__list {
	@extend %global_flex_block_row_wrap_flex-start_stretch;

	@include bp($point_2, min) {
		overflow: visible !important;
		.swiper-wrapper {
			flex-wrap: wrap;
			margin: 0 -4rem -4rem 0;
		}
	}

	@include bp($point_3) {
		margin: 0 calc(var(--gl-indent) * -1) -3.3rem calc(var(--gl-indent) * -1) !important;

		.swiper-wrapper {
			padding-left: 5rem;
		}
	}
}

.news__item {
	height: auto !important;
	
	@include bp($point_2, min) {
		padding: 0 4rem 7rem 0;
		width: 25% !important;
	}

	@include bp($point_3) {
		width: 30rem !important;
		padding: 0 2rem 5.3rem 0;
	}
}

.news__link {
	position: relative;

	display: block;
	height: 100%;
	padding: 0 0 6.8rem;

	color: $accent_color_2;
	text-align: center;

	box-shadow: 0 1.2rem 1.2rem rgba(0, 0, 0, 0.1);

	&:hover, &:focus {
		text-decoration: none;

		&:before {
			transform: scaleY(1);
		}
	}

	&:before {
		content: '';
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;

		transform: scaleY(0);
		transform-origin: 0 100%;

		background-color: $accent_color_1;
		transition: transform .3s;

		z-index: 1;
	}
}

.news__img {
	position: relative;
	display: block;
	margin: 0 0 2rem;

	&:before {
		content: '';
		display: block;
		padding-bottom: 85.6%;
	}
}

.news__img_in {
	@extend %cover_img;

	position: absolute;
	top: 0;
	left: 0;
}

.news__content {
	position: relative;
	padding: 0 2rem;
	z-index: 2;

	transition: transform .3s;

	.news__link:hover &,
	.news__link:focus & {
		color: $white;
		transform: translateY(-9rem);
	}
}

.news__date {
	margin-bottom: 1.8rem;

	font-size: 1.2rem;
	font-weight: 700;
}

.news__name {
	font-size: 1.6rem;
	line-height: 1.2;
}

.news__more {
	@extend %global_flex_block_row_nowrap_center_center;

	position: absolute;
	bottom: -3rem;
	left: 50%;

	width: 5.4rem;
	height: 6rem;
	margin-left: -2.7rem;

	color: $white;

	transform-origin: color .3s, background-color .3s;

	z-index: 3;

	.news__link:hover &,
	.news__link:focus & {
		color: $accent_color_1;
		filter: drop-shadow(0 1.2rem 1.2rem rgba(0, 0, 0, 0.1));
	}
}

.news__decor {
	position: absolute;
	top: 0;
	left: 0;

	width: 100%;
	height: 100%;

	color: $accent_color_2;

	z-index: -1;

	.news__link:hover &,
	.news__link:focus & {
		color: $white;
	}

	svg {
		display: block;
		width: 100%;
		height: 100%;
		fill: currentColor;
	}
}

.news__arrow {
	width: 1.8rem;
	height: 1rem;
}

.news_scrollbar {
	position: relative;
	height: 2rem;
	width: calc(100% + 4rem);
	margin: 5.2rem -2rem 0;

	cursor: pointer;
	z-index: 3;

	&::after {
		z-index: -1;
		position: absolute;
		top: 50%;
		left: 0;
		width: 100%;
		height: 1px;

		background-color: #d8d8d8;
		content: '';
	}

	.swiper-scrollbar-drag {
		height: 2rem;
		margin-top: -1rem;

		background: url('../images/scrollbar_decor.svg') no-repeat center center;
		background-size: 2rem 2rem;
	}

	@include bp($point_2, min) {
		display: none;
	}
}


.news__btn {
	@extend %global_flex_block_row_nowrap_center;
}

.news__btn_in {
	@extend %btn_size_1;
	@extend %default_btn;
}