.directa_plus {
	position: relative;

	//padding-top: 33rem;

	@include bp($point_2, min) {
		padding-top: 15.5rem;
		padding-bottom: 12.7rem;
	}

	@include bp($point_3, $point_4) {
		padding-bottom: 40vw;
	}

	@include bp($point_4 - 1) {
		padding-bottom: 86.6vw;
	}
}

.directa_plus__in {
	max-width: $page_width_2;
}

.directa_plus_col_wrap {
	@include bp($point_2, min) {
		@include flex_block(row, nowrap, flex-start, flex-start);
	}
}

.directa_plus_col {
	@include bp($point_2, min) {
		width: 50%;
	}

	&.directa_plus_col--pic {
		@include bp($point_2, min) {
			padding-bottom: 50%;
		}

		@include bp($point_3, $point_6) {
			padding-bottom: 55%;
		}

		@include bp($point_6 - 1) {
			padding-bottom: 100%;
		}
	}
}

.directa_plus_pic {
	position: absolute;
	left: 0;
	height: auto;

	@include bp($point_2, min) {
		width: 72%;
		margin-top: -17.4%;
		margin-left: -31%;
	}

	@include bp($point_3, $point_6) {
		width: 110%;
		margin-top: -42.8%;
		margin-left: -47.2%;
	}

	@include bp($point_6 - 1) {
		width: 167%;
		margin-top: -60.8%;
		margin-left: -75.2%;
	}
}

.directa_plus_img {
	@extend %cover_img;
}

.directa_plus_img__in {
	top: -1.186rem;
	left: 12.319rem;
	width: 120rem * 1.41;
	height: 80rem * 1.42;
}

.directa_plus__content {
	max-width: var(--width-content);

	@include bp($point_2, min) {
		padding-left: 1.8rem;
	}

	@include bp($point_3) {
		text-align: center;
		margin: 0 auto;
	}
}

.directa_plus__title {
	@extend %h1;

	@include bp($point_2, min) {
		margin-bottom: 1.4rem;
	}

	@include bp($point_3) {
		margin-bottom: .8rem;
	}
}

.directa_plus__descrip {
	@extend %descrip;

	@include bp($point_2, min) {
		max-width: 71rem;
		margin-bottom: 6.2rem;
	}

	@include bp($point_3) {
		margin-bottom: 3.8rem;
	}
}

.directa_plus__btn {
	@extend %btn_size_1;
	@extend %default_btn;
}



.directa_plus__decors {

}

.directa_plus__decor {
	z-index: -1;
	position: absolute;
	bottom: 0;
	right: 0;

	display: block;
	margin: 0 10% 20% 0;

	&:nth-child(3) {
		// width: 43.4rem;
		// height: 48.2rem;

		@include bp($point_2, min) {
			width: 22.6vw;
			height: 25.1vw;
			margin: 0 2.8% 6% 0;
		}
		@include bp($point_3) {
			width: 61.2vw;
			height: 66.1vw;
			margin: 0 14% 7.6% 0;
		}
	}
	&:nth-child(2) {
		// width: 17.4rem;
		// height: 19.2rem;

		@include bp($point_2, min) {
			width: 9.1vw;
			height: 10vw;
			margin: 0 -2% 26.4% 0;
		}
		@include bp($point_3) {
			width: 23.2vw;
			height: 25.6vw;
			margin: 0 3.4% 61% 0;
		}
	}
	&:nth-child(1) {
		// width: 30.4rem;
		// height: 33.6rem;

		@include bp($point_2, min) {
			width: 15.8vw;
			height: 17.5vw;
			margin: 0 -6% -6.2% 0;
		}
		@include bp($point_3) {
			width: 40.6vw;
			height: 45vw;
			margin: 0 -6.6% -23.4% 0;
		}
	}
}

.directa_plus__decor_img {
	@extend %contain_img;
}
