//---------------------------------------------logo
.logo {
	display: block;

	@include bp($point_2, min) {
		width: 35.6rem;
		height: 3.6rem;
	}

	@include bp($point_3) {
		width: 5rem;
		height: 3.6rem;
	}

	.main_menu__header & {
		pointer-events: auto;
	}
}

.logo__img,
.logo__in {
	display: block;
	width: 100%;
	height: 100%;

	object-fit: contain;
}
